import React from 'react'
import styles from './Registration.module.scss'
import { useForm } from "react-hook-form";
import icon_alert from './../components/pics/icon_alert.png'
import icon_chat from './../components/pics/icon_chat.png'
import icon_profile from './../components/pics/icon_profile.png'
import icon_publish from './../components/pics/icon_publish.png'
import { Link, useLocation, useNavigate } from 'react-router-dom'

import { useGlobalContext } from './../redux/actions'


function Registration() {
    const location = useLocation();
    const { register, handleSubmit, watch, formState: { errors } } = useForm();
    const navigate = useNavigate()
    const { registration } = useGlobalContext()

    function onSubmit(data) {
        registration([data, location.state.data])
        navigate('../login-register')
        // document.getElementById('navbar').style.display = 'flex'
    }


    return (
        <div className={styles.mainBG}>
            <div><  img className={styles.imgpadding} src='https://assets.yad2.co.il/personal/images/general/new_logo_orange.png'></img></div>  {/* navbar img*/}

            <div className={styles.whiteBG}>


                <form className={styles.column} >

                    <label className={styles.title2}> שם פרטי*</label>
                    <input placeholder='הקלדת שם פרטי'  {...register('name')}></input>



                    <label className={styles.title2}> שם משפחה*</label>
                    <input placeholder='  הקלדת שם משפחה' {...register('family')}></input>


                    <label className={styles.title2}>   מספר טלפון*</label>
                    <input placeholder='      מספר נייד' {...register('mobilephone')}></input>

                    <div className={styles.flex}><input className={styles.box} type='checkbox'></input><div className={styles.title5}>קראתי ואישרתי את תקנון האתר</div></div>

                    <div className={styles.flex}><input className={styles.box} type='checkbox'></input><div className={styles.title5}  >      אני רוצה לקבל דיוור פרסומי כללי מיד2    </div></div>



                    <button type='submit' onClick={handleSubmit(onSubmit)} className={styles.btnOrange}>הרשמה</button>


                </form>










            </div>

















            <div className={styles.margin1}  >{/* left div*/}

                <div className={styles.largetitle} >לקנות מהר, למכור מהר.</div>
                <div className={styles.largetitle} >ויש לנו אחלה כלים לזה.</div>

                <div className={styles.flex}>{/* left div pics*/}
                    <div className={styles.column}>
                        <img className={styles.imgsize} src={icon_alert}></img>
                        <div className={styles.title2}>התראות</div>
                    </div>

                    <div className={styles.column}>
                        <img className={styles.imgsize} src={icon_chat}></img>
                        <div className={styles.title2}>צ׳אט</div>
                    </div>

                    <div className={styles.column}>
                        <img className={styles.imgsize} src={icon_profile}></img>
                        <div className={styles.title2}>פרסום מודעה</div>
                    </div>

                    <div className={styles.column}>
                        <img className={styles.imgsize} src={icon_publish}></img>
                        <div className={styles.title2}>אזור אישי</div>
                    </div>

                </div>

            </div>










        </div>
    )
}

export default Registration