import styles from './Results.module.scss'
import ResultExpand from './ResultExpand'
import { Map, Marker } from "pigeon-maps"

import React, { useState, useEffect } from 'react';
import { exp, condense } from '../functions/utils.js'
import { filterResults, activateInnerOrange } from '../functions/utilsFilterAndOrder.js'
import { orderByPaymentPath } from '../functions/utilsFilterAndOrder.js'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronDown } from '@fortawesome/free-solid-svg-icons';
import { faFilter } from '@fortawesome/free-solid-svg-icons';
import { faLocationPin } from '@fortawesome/free-solid-svg-icons';
import { faShekelSign } from '@fortawesome/free-solid-svg-icons';
import { faHeart } from '@fortawesome/free-solid-svg-icons';
import InfiniteScroll from 'react-infinite-scroll-component';
import sidebar from '../../components/pics/sidebar.png'
import heart from '../../components/pics/heart.svg'
import orangeHeart from '../../components/pics/orangeHeart.png'
import { changeTextToOrange, cancelChanges } from '../Navbar/navbarUtils'

import { useGlobalContext } from '../../redux/actions'
import { changeHeartColor } from '../functions/utils.js'
function Results({ results, setresults }) {
    const [id_, setid_] = useState(null)
    const [expandedResult, setexpandedResult] = useState({})
    const [greyshadow1, setGreyshadow1] = useState(false)
    const [greyshadow2, setGreyshadow2] = useState(false)
    const [filter, setfilter] = useState(false)
    const [checkboxprice, setcheckboxprice] = useState(false)
    const [checkboximg, setcheckboximg] = useState(false)
    const [orderoptions, setorderoptions] = useState(false)
    const [isOpen, setisOpen] = useState(false)
    const [map, setmap] = useState(false)
    const { favouriteAdds } = useGlobalContext()

    useEffect(() => { if (id_ != null) document.getElementById(id_).style.display = 'flex' }, [id_, expandedResult])

    orderByPaymentPath(results, setresults)

    function saveAdd(e, data) {
        e.stopPropagation()
        favouriteAdds(data)
    }


    return (
        <div className={styles.methawrapper} >
            { /* <div className={styles.sidebarwrapper} >  <img src={sidebar} className={styles.sidebars} ></img></div>*/}
            <div className={styles.main}>

                <div className={styles.mainwrapper2}>

                    <div className={styles.mainwrapperfilter}  >
                        <div className={styles.regtext}  >מיין לפי</div>
                        <div
                            className={styles.datewrapper} >
                            <div className={styles.filter} >לפי תאריך  </div>
                            <FontAwesomeIcon icon={faChevronDown} onClick={() => setorderoptions(!orderoptions)} ></FontAwesomeIcon>
                        </div>
                        {orderoptions &&
                            <div className={styles.orderwrapper}>
                                <div className={styles.flexorder}>
                                    <div id='date_' className={styles.outerorange} onClick={(e) => activateInnerOrange(e.target.id, results, setresults)}>
                                        <div id='date' className={styles.innerorange}></div></div>
                                    <div className={styles.regulartext} >לפי תאריך</div>
                                </div>

                                <div className={styles.flexorder}>
                                    <div id='priceup_' className={styles.outerorange} onClick={(e) => activateInnerOrange(e.target.id, results, setresults)} >
                                        <div id='priceup' className={styles.innerorange}></div></div>
                                    <div className={styles.regulartext} >  מחיר - מהזול ליקר</div>
                                </div>

                                <div className={styles.flexorder}>
                                    <div id='pricedown_' className={styles.outerorange} onClick={(e) => activateInnerOrange(e.target.id, results, setresults)} >
                                        <div id='pricedown' className={styles.innerorange}></div></div>
                                    <div className={styles.regulartext} >  מחיר - מהיקר לזול</div>
                                </div>
                            </div>
                        }
                    </div>


                    <div className={styles.flexrow} >                               {/* סינון */}
                        <div


                            className={(!greyshadow1) && (!checkboximg && !checkboxprice) ? styles.datewrapper1 :
                                (greyshadow1) && (!checkboximg && !checkboxprice) ? styles.datewrapperalt1 :
                                    (checkboximg || checkboxprice) ? styles.datewrapperalt2 :
                                        styles.datewrapper1} >


                            <div onMouseEnter={() => setGreyshadow1(!greyshadow1)}
                                onMouseLeave={() => setGreyshadow1(!greyshadow1)}
                                onClick={() => setfilter(!filter)}
                                className={styles.innerfascio} >
                                <FontAwesomeIcon className={styles.filteicon} icon={faFilter} ></FontAwesomeIcon>
                                <div>סנן תוצאות</div>
                            </div>

                            {filter &&
                                <div className={styles.filterexpandmain} >


                                    <div className={styles.checwrap}>
                                        <input id='price' className={styles.checinp} type='checkbox'
                                            onClick={(e) => {
                                                e.stopPropagation()
                                                setcheckboxprice(true)

                                            }} ></input>
                                        <div>עם מחיר</div>
                                    </div>
                                    <div className={styles.checwrap} >
                                        <input id='img' className={styles.checinp} type='checkbox'
                                            onClick={(e) => {
                                                e.stopPropagation()
                                                setcheckboximg(true)

                                            }}
                                        ></input>
                                        <div>עם תמונה</div>
                                    </div>
                                    <div className={styles.bar}  ></div>

                                    <div className={styles.buttons} >
                                        <div className={checkboximg ? styles.greyactive :
                                            checkboxprice ? styles.greyactive :
                                                styles.greybutton
                                        }
                                            onClick={() => {
                                                document.getElementById("price").checked = false
                                                document.getElementById("img").checked = false
                                                setcheckboximg(false)
                                                setcheckboxprice(false)


                                            }} >ניקוי</div>


                                        <div className={styles.orangebutton}
                                            onClick={() => filterResults(results, setresults, checkboxprice,
                                                setfilter, checkboximg, setGreyshadow1, setcheckboximg, setcheckboxprice)}>סינון</div>
                                    </div>

                                </div>
                            }
                        </div>

                        <div onMouseEnter={() => setGreyshadow2(!greyshadow2)}
                            onMouseLeave={() => setGreyshadow2(!greyshadow2)}
                            className={!greyshadow2 ? styles.datewrapper : styles.other} >
                            <FontAwesomeIcon icon={faLocationPin}  ></FontAwesomeIcon>

                            <div onClick={() => setmap(true)}>תצוגת מפה

                            </div>

                        </div>
                        {map &&
                            <Map height={300} defaultCenter={[50.879, 4.6997]} defaultZoom={11}>
                                <Marker width={50} anchor={[50.879, 4.6997]} />
                            </Map>}

                    </div>


                </div>


                {results.map((r, index) =>

                    <div id={r.M_id} onClick={() => exp(r, index, setid_, setexpandedResult, isOpen, setisOpen)}
                        className={
                            r.paymentPath == 3 ? 'mainwrapperultra' :
                                r.paymentPath == 2 ? 'mainwrappersuper' :
                                    'mainwrapper'}
                        onMouseEnter={() => {
                            changeTextToOrange(r.M_id)
                            document.getElementById(r.M_id).style.boxShadow = '2px 2px 4px 4px rgb(12 0 51 / 20%)'
                        }}

                        onMouseLeave={() => {
                            cancelChanges(r.M_id)
                            document.getElementById(r.M_id).style.boxShadow = 'none'
                        }}


                    >


                        <div id={r.M_id + 1} className={styles.frame1}  >  {/* first section (right one) */}

                            <div className={styles.frame3}>


                                <div onClick={(e) => {
                                    saveAdd(e, r)
                                    changeHeartColor(index)
                                }}>
                                    <div className={styles.heartwrapper}  >
                                        <img id={index} className={styles.hollowheart} src={heart}></img>
                                        <img id={index + 100} className={styles.hollowheartAlt} src={orangeHeart}></img>

                                    </div>
                                    {r.main && <img className={styles.img} src={r.main} ></img>}
                                    {!r.main && <div className={styles.noimg2}  >ללא תמונה</div>}
                                </div>




                            </div>

                            <div className={styles.frame4} >

                                <div className={styles.inner1}>
                                    <div className={styles.streetmargin}>{r.street}</div>
                                    <div>{r.houseNumber}</div>
                                </div>

                                <div className={styles.inner2} >
                                    <div className={styles.streetmargin} >{`${r.settlement}, `}</div>
                                    <div>{r.kind}</div>
                                </div>




                            </div>


                        </div>


                        <div id={r.M_id + 2} className={styles.frame2}  >

                            <div className={styles.column}>
                                <div className={styles.numbers} >{r.numberOfRooms}</div>
                                <div className={styles.numberstitles}  >חדרים</div>
                            </div>

                            <div className={styles.column}>
                                <div className={styles.numbers} >{r.floorNumber}</div>
                                <div className={styles.numberstitles}  >קומה</div>
                            </div>

                            <div className={styles.column}>
                                <div className={styles.numbers} >{r.quadrateMeterTotal}</div>
                                <div className={styles.numberstitles}  >מ"ר</div>
                            </div>
                        </div>



                        <div id={r.M_id + 5} className='frame5' >


                            <div className={styles.niswrapper} >
                                <div className='white' id={`uppertext${r.M_id}`}> פתיחה בטאב חדש</div>

                                <div className={styles.nisflex} >
                                    {(r.price > 0) ? r.price : 'אין מחיר'}
                                    {(r.price > 0) && <FontAwesomeIcon className={styles.nissign} icon={faShekelSign} ></FontAwesomeIcon>}
                                </div>

                                <div id={`lowertext${r.M_id}`} className={styles.updatetext} > עודכן היום    </div>
                            </div>

                        </div>
                    </div>)}



                <ResultExpand id_={id_} setid_={setid_} expandedResult={expandedResult} setexpandedResult={setexpandedResult} condense={condense} setisOpen={setisOpen}
                ></ResultExpand>








            </div>
            {         /*   <div className={styles.sidebarwrapper} ><img src={sidebar} className={styles.sidebars} ></img></div>*/}
        </div>

    )
}

export default Results