import React from 'react'
import styles from './Img2.module.scss'

import download_qr from '../pics/download_qr.png'
import android from '../pics/download_android.png'
import apple from '../pics/download_iphone.png'
import jinjit2 from '../pics/jinjit1.png'
import yadBG from '../pics/yadBG.png'
function Img2() {
    return (
        <div className={styles.flexrow} >
            <img className={styles.imgBG} src={yadBG}></img>
            <div className={styles.flexrow} >
                <div>
                    <div className={styles.largesttitle}>מחפשים דירה חדשה?</div>
                    <div className={styles.middletitle}>מתחם בלעדי של פרויקטים חדשים במבצעים מיוחדים!</div>
                    <button className={styles.orangebutton}>היכנסו תיק תק!</button>

                </div>

                <div className={styles.wrapper}> <img className={styles.jinjit2} src={jinjit2}></img></div>


            </div>
        </div>
    )
}

export default Img2