import axios from "axios"


function reducer(state, action) {





    //if (action.type === 'LOG_USER_IN') { return { ...state, isLogged: true, loggedName: action.payload.mail } }

    if (action.type === 'FORM_STEP_FORWARD') { return { ...state, REformFlow: (action.payload.currentFormStep + 1) } }
    if (action.type === 'FORM_STEP_BACK') { return { ...state, REformFlow: (action.payload.currentFormStep - 1) } }
    if (action.type === 'FORM_STEP_SPECIFIC') { return { ...state, REformFlow: action.payload } }
    if (action.type === 'UPDATE_ABSOLUTE_STEP') { return { ...state, REformFlowAbsolute: (action.payload.currentFormStep + 1) } }



    // step2
    if (action.type === 'SAVE_SETTLEMENT') { return { ...state, Object: { ...state.Object, settlement: action.payload.s } } }
    if (action.type === 'SAVE_STREET') { return { ...state, Object: { ...state.Object, street: action.payload } } }


    if (action.type === 'SAVE_FORM_2_DATA') {

        return {
            ...state, Object: {
                ...state.Object,
                kind: action.payload[0].kind,
                condition: action.payload[0].condition,
                floorNumber: action.payload[0].floorNumber,
                totalFloorNumber: action.payload[0].totalFloorNumber,
                houseNumber: action.payload[1]
            }
        }
    }


    //step3

    if (action.type === 'SAVE_ROOMS') { return { ...state, Object: { ...state.Object, numberOfRooms: action.payload } } }
    if (action.type === 'SAVE_PARKINGS') { return { ...state, Object: { ...state.Object, numberOfParkings: action.payload } } }
    if (action.type === 'SAVE_BALCONIES') { return { ...state, Object: { ...state.Object, numberOfBalconies: action.payload } } }
    if (action.type === 'SAVE_FORM_3_DATA') {
        return {
            ...state, NonMandatoryObject: {
                ...state.NonMandatoryObject,
                bars: action.payload.bars, boiler: action.payload.boiler, citchen: action.payload.citchen,
                disabled: action.payload.disabled, elevator: action.payload.elevator, furniture: action.payload.furniture, mamad: action.payload.mamad,
                mazgan: action.payload.mazgan, repaired: action.payload.repaired, shelter: action.payload.shelter, tadiran: action.payload.tadiran,
                description: action.payload.description
            }
        }
    }


    //step4

    if (action.type === 'SAVE_FORM_4_DATA') {
        return {
            ...state, Object: {
                ...state.Object,
                quadrateMeter: action.payload.data.quadrateMeter,
                quadrateMeterGarden: action.payload.data.quadrateMeterGarden,
                quadrateMeterTotal: action.payload.data.quadrateMeterTotal
            },
            NonMandatoryObject: {
                ...state.NonMandatoryObject,
                price: action.payload.data.price,
                enteringDate: action.payload.startDateModified
            }
        }
    }

    //step5

    if (action.type === 'SAVE_FORM_5_DATA') {
        return {
            ...state, Pictures: {
                ...state.Pictures,
                main: action.payload.main,
                one: action.payload.one,
                two: action.payload.two,
                three: action.payload.three,
                four: action.payload.four,
                five: action.payload.five,
                six: action.payload.six,
                seven: action.payload.seven,
                eight: action.payload.eight
            }
        }

    }

    //step6

    if (action.type === 'SAVE_FORM_6_DATA') {
        return {
            ...state, Object: {
                ...state.Object,
                contactName: action.payload.contactName,
                phone: action.payload.mainPhoneNumber
            },
            NonMandatoryObject: {
                ...state.NonMandatoryObject,
                additionalContactName: action.payload.additionalContactName,
                additionalPhone: action.payload.additionalPhone,
                email: action.payload.email
            }
        }

    }

    //step7

    // if (action.type === 'SAVE_FORM_7_DATA') { return { ...state, Object: { ...state.Object, paymentPath: action.payload } } }

    if (action.type === 'SAVE_FORM_7_DATA') {




        axios.post(`http://localhost:8080/post`, [action.payload, state]).then((res) => {
            console.log(res.data)
            return {
                ...state, REformFlow: 1, REformFlowAbsolute: 1
            }
        })
    }


    //general

    if (action.type === 'REGISTRATION') {
        axios.post(`http://localhost:8080/register`, action.payload).then((res) => {
            console.log(res.data)
        })
    }

    if (action.type === 'LOGIN') {
        return {
            ...state, isLogged: true, loggedName: action.payload.name,
            loggedfamilyname: action.payload.family, loggedmail: action.payload.mail, loggedphone: action.payload.loggedphone
        }
    }

    if (action.type === 'DISCONNECT') {
        return {
            ...state, isLogged: false, loggedName: '', loggedfamilyname: '', loggedmail: ''
        }
    }




    if (action.type === 'SAVE_FAVOURITE_DATA') {
        return {
            ...state, favouriteFunctions: action.payload
        }
    }

    if (action.type === 'UPDATE_FAV_ADD') {
        return {
            ...state, favAdd: action.payload
        }
    }
    if (action.type === 'RESTORE_FAV_ADD') {
        return {
            ...state, favAdd: [...state.favAdd, action.payload]
        }
    }


    //search

    if (action.type === 'RE_PROPS') {
        return {
            ...state, Search: {
                ...state.Search, REprops: action.payload
            }
        }
    }


    if (action.type === 'N_ROOMS') {
        if (action.payload[1] === 'from') {
            return {
                ...state, Search: {
                    ...state.Search, roomsFrom: action.payload[0]
                }
            }
        }
        else if (action.payload[1] === 'to') {
            return {
                ...state, Search: {
                    ...state.Search, roomsTo: action.payload[0]
                }
            }
        }

    }

    if (action.type === 'SAVE_SEARCH_PROPS') {
        return {
            ...state, AdvancedSearch: {
                ...state.search,
                AC: action.payload[0].AC,
                balcony: action.payload[0].balcony,
                bars: action.payload[0].bars,
                disabled: action.payload[0].disabled,
                elevator: action.payload[0].elevator,
                furniture: action.payload[0].furniture,
                parking: action.payload[0].parking,
                remonted: action.payload[0].remonted,
                shelter: action.payload[0].shelter,
                solitary: action.payload[0].solitary,
                warehouse: action.payload[0].warehouse,
                description: action.payload[0].description,
                floorFrom: action.payload[0].floorFrom,
                floorTo: action.payload[0].floorTo,
                sizeFrom: action.payload[0].sizeFrom,
                sizeTo: action.payload[0].sizeTo,
                enteringDate: action.payload[1]

            }
        }
    }



    if (action.type === 'SAVE_SEARCH_INFO') {
        return {
            ...state, commitedSearches: [...state.commitedSearches, action.payload]

        }
    }




    //personal zone


    if (action.type === 'SAVE_ADD') {
        let tester = false
        for (let i = 0; i < state.favAdd.length; i++) {
            if (state.favAdd[i].M_id === action.payload.M_id) tester = true
        }


        if (!tester) {
            return {
                ...state, favAdd: [...state.favAdd, action.payload]
            }
        }

    }



    return state

}

export default reducer