import React, { useState, useEffect } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import styles from './Navbar.module.scss'
import { useGlobalContext } from './redux/actions'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars, faDoorOpen } from '@fortawesome/free-solid-svg-icons'
import bell from './components/pics/bell.png'
import user from './components/pics/person.png'
import heart from './components/pics/heart.svg'
import FavModal from './components/Navbar/FavModal'
import NavbarMenu from './components/Navbar/NavbarMenu'
import PersonalInfoNotLogged from './components/Navbar/OriginalNavBarComponents/PersonalInfoNotLogged'
import PersonalInfoLogged from './components/Navbar/OriginalNavBarComponents/PersonalInfoLogged'
import { heartInfo } from './utils'


function Navbar() {
    const navigate = useNavigate()
    const { isLogged, loggedName, isLocationLogreg, setisLocationLogreg, favAdd, loggedfamilyname, disconnect } = useGlobalContext()
    const [menu, setMenu] = useState(false)
    const [numberOfFavoriteAdds, setnumberOfFavoriteAdds] = useState(0)
    const [emptyAddInfo, setemptyAddInfo] = useState(false)
    const [hoveringoverheart, sethoveringoverheart] = useState(false)
    const [activateprivateAreaInfo, setactivateprivateAreaInfo] = useState(false)
    const [activateprivateAreaInfominus, setactivateprivateAreaInfominus] = useState(false)

    function eliminateNavbar() { document.getElementById('navbar').style.display = 'none' }
    useEffect(() => { setnumberOfFavoriteAdds(favAdd.length) }, [favAdd])




    return (

        <>


            <div id='navbar' className={styles.navbar}>
                <div id='right-side' className={styles.row}>{/* right side navbar*/}

                    <div onClick={() => setMenu(!menu)} className={styles.row}>
                        <div className={styles.righttitle}>תפריט</div>
                        <FontAwesomeIcon className={styles.baricon} icon={faBars} ></FontAwesomeIcon>

                    </div>
                    <img onClick={() => navigate('..')} className={styles.img} src='https://assets.yad2.co.il/personal/images/general/new_logo_orange.png'></img>



                </div>

                <div id='left-side' className={styles.leftSideIcons}>  {/* left side navbar*/}

                    <Link onClick={() => eliminateNavbar()} className={styles.greenbutton} to='./login-register' state={'publish-login'}>   + פרסום מודעה חדשה      </Link>

                    <div id='icons' className={styles.marginauto}>

                        {/* user icon*/}
                        <div className={styles.row_grey}
                            onMouseEnter={() => setactivateprivateAreaInfominus(true)}

                        >

                            {!isLogged &&

                                <Link

                                    onClick={() => {
                                        setactivateprivateAreaInfominus(false)
                                        eliminateNavbar()
                                    }} className={styles.greycolor} to='./login-register' state={'regular-login'}>
                                    <div className={styles.userBG}>
                                        <img className={styles.user} src={user} ></img>
                                    </div>

                                </Link>
                            }




                            {isLogged &&
                                <div className={styles.loggedname}
                                    onMouseEnter={() => setactivateprivateAreaInfo(true)} >
                                    {loggedfamilyname.slice(0, 1) + loggedName.slice(0, 1)}</div>
                            }


                        </div>

                        {(activateprivateAreaInfominus && !isLogged) && <PersonalInfoNotLogged
                            setactivateprivateAreaInfo={setactivateprivateAreaInfo}
                            setactivateprivateAreaInfominus={setactivateprivateAreaInfominus} />
                        }

                        {activateprivateAreaInfo && <PersonalInfoLogged
                            setactivateprivateAreaInfo={setactivateprivateAreaInfo}
                            setactivateprivateAreaInfominus={setactivateprivateAreaInfominus} disconnect={disconnect} />
                        }



                        <div className={styles.row_greyfav}
                            onMouseEnter={() => heartInfo(numberOfFavoriteAdds, sethoveringoverheart, setemptyAddInfo, setactivateprivateAreaInfominus, setactivateprivateAreaInfo)}
                            onMouseLeave={() => {
                                sethoveringoverheart(false)
                                setemptyAddInfo(false)
                            }} >

                            <div className={styles.numbersinheart}  >{numberOfFavoriteAdds}</div>
                            <Link className={styles.tofavourites} to='./favPage' > <img className={styles.user} src={heart} /></Link>



                            {hoveringoverheart &&
                                <FavModal
                                    sethoveringoverheart={sethoveringoverheart} ></FavModal>}




                            {emptyAddInfo &&
                                <div className={styles.mainInfonoAdd}  >
                                    <div className={styles.noInfoTitle} >מודעות שאהבתי</div>
                                    <div>הרשימה שלך עדיין ריקה אפשר להוסיף מודעות לרשימה בלחיצה על הסימן בפינה הימנית של כל מודעה</div>
                                </div>}
                        </div>



                        <div className={styles.row_grey} >   <img className={styles.user} src={bell} ></img></div>
                    </div>



                </div>

            </div>






            {menu && <NavbarMenu setMenu={setMenu} isLogged={isLogged} eliminateNavbar={eliminateNavbar} menu={menu} loggedName={loggedName} />}




        </>

    )
}

export default Navbar



