import React, { useState, useEffect } from 'react'
import styles from './SearchRE.module.scss'
import header from './../pics/header.png'

import Kind from './BasicComponents/Kind'
import Results from './Results'
import Rooms from './BasicComponents/Rooms'
import AdvancedSearch from './AdvancedSearch'
import Icons from './BasicComponents/Icons'
import FindCity from './BasicComponents/FindCity'
import axios from 'axios'
import { findSettlement } from '../functions/utils.js'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlus } from '@fortawesome/free-solid-svg-icons'
import { faMagnifyingGlass } from '@fortawesome/free-solid-svg-icons'
import { useGlobalContext } from '../../redux/actions'
import { faGreaterThan } from '@fortawesome/free-solid-svg-icons'
import { faBell } from '@fortawesome/free-solid-svg-icons'
import { rotate } from '../functions/utils.js'
import { basicSearch, advancedSearch } from '../functions/searchResults.js'
import { initialNoSearchResults, handleAdress } from '../functions/utils'
import InfiniteScroll from 'react-infinite-scroll-component';


function SearchRE() {
    const { Nrooms, Search, loggedName } = useGlobalContext()
    const [Activatekind, setActivatekind] = useState(false)
    const [ActivateRooms, setActivateRooms] = useState(false)
    const [priceFrom, setpriceFrom] = useState(false)
    const [priceTo, setpriceTo] = useState(false)
    const [city, setCity] = useState([])
    const [street, setStreet] = useState([])
    const [chosenCity, setchosenCity] = useState('')
    const [chosenStreet, setchosenStreet] = useState('')
    const [roomsAmount, setroomsAmount] = useState('')
    const [adress, setadress] = useState('')
    const [results, setresults] = useState([])
    const [adressValue, setadressValue] = useState('')
    const [estateKind, setestateKind] = useState([])
    const [estateArray, setestateArray] = useState([])
    const [roomNumberFrom, setroomNumberFrom] = useState('')
    const [roomsNumberTo, setroomsNumberTo] = useState('')
    const [advancedSearchPanel, setadvancedSearchPanel] = useState(false)
    const [resultAmount, setresultAmount] = useState(10)
    const [transfer, settransfer] = useState([])
    const [roomsmin, setroomsmin] = useState('')
    const [roomsmax, setroomsmax] = useState('')
    const [roomsinterval, setroomsinterval] = useState('')
    const [roomsArr, setroomsArr] = useState([])

    useEffect(() => {
        initialNoSearchResults(setresults, 10)
    }, [])



    function handleSearchType() {
        basicSearch(chosenCity, chosenStreet, Search, priceFrom, priceTo,
            roomNumberFrom, roomsNumberTo, setresults, resultAmount)
        setroomsArr([])
    }

    function fetchData() {
        if (chosenCity === '' && advancedSearchPanel === false) {
            initialNoSearchResults(setresults, resultAmount)
            setresultAmount(resultAmount + 3)
        }
    }


    useEffect(() => {
        if (roomsmin !== '' && roomsmax === '') setroomsinterval(`מ${roomsmin}`)
        else if (roomsmin === '' && roomsmax !== '') setroomsinterval(`עד${roomsmax} `)
        else if (roomsmin !== '' && roomsmax !== '') setroomsinterval(`  מ${roomsmin} עד${roomsmax}    `)

    }, [roomsmax, roomsmin])



    return (
        <div className={styles.flexcol} >

            <div className={styles.upperbar}></div>
            <div className={styles.headerwrapper}>
                <img className={styles.header} src={header}></img>

            </div>
            <div id='mainBG' className={styles.mainBG}>
                <div className={!advancedSearchPanel ? styles.whiteBG : styles.whiteBGalt}>

                    <div className={styles.searchtop}>

                        <div className={styles.titlelarge} >איזה נכס <span className={styles.titlelargeorange}>למכירה</span>  תרצו לחפש?</div>
                        <div className={styles.iconflex}><FontAwesomeIcon className={styles.bell} icon={faBell} /> <div>קבלו התראות במייל על החיפוש</div> </div>

                    </div>

                    <div className={styles.searchdown}>
                        <div className={styles.column}>
                            <label className={styles.up}  >חפשו אזור, עיר, שכונה או רחוב</label>

                            <input value={adressValue} onChange={(e) => {
                                setadressValue(e.target.value)
                                findSettlement(e.target.value, setStreet, setCity)
                            }}
                                className={styles.city} placeholder='לדוגמה: פארק הים'></input>



                            {(city.length > 0 || street.length > 0) &&

                                <FindCity city={city} setadressValue={setadressValue} handleAdress={handleAdress} setCity={setCity} setStreet={setStreet}
                                    setadress={setadress} setchosenCity={setchosenCity} setchosenStreet={setchosenStreet} street={street}

                                />
                            }



                            <div className={styles.adresswrapper}>

                            </div>
                        </div>

                        <div className={styles.column}>
                            <label  >סוג נכס</label>
                            <div>
                                <div className={styles.fontwrapper1}>  <FontAwesomeIcon className={!Activatekind ? styles.greatersign : styles.greatersignalt} icon={faGreaterThan}></FontAwesomeIcon></div>
                                <input value={estateKind} className={styles.select} onClick={() => setActivatekind(!Activatekind)} placeholder='בחרו סוג נכסים'></input>
                            </div>
                            {/* all kinds of  RE !!!!!  */}
                            {Activatekind &&
                                <Kind setestateArray={setestateArray} setestateKind={setestateKind} estateKind={estateKind} setActivatekind={setActivatekind} className={styles.abs}></Kind>}
                        </div>


                        <div className={styles.column}>
                            <label>  חדרים</label>
                            <div>
                                <div className={styles.fontwrapper2}  >  <FontAwesomeIcon className={!ActivateRooms ? styles.greatersign2 : styles.greatersignalt2} icon={faGreaterThan}></FontAwesomeIcon></div>

                                <input value={roomsinterval} className={styles.rooms} onClick={() => setActivateRooms(!ActivateRooms)} placeholder='חדרים'></input>
                            </div>

                            {/* RE rooms !!!!!  */}
                            {ActivateRooms &&
                                <Rooms setroomsmin={setroomsmin} setroomsmax={setroomsmax} setroomNumberFrom={setroomNumberFrom}
                                    setroomsNumberTo={setroomsNumberTo} setActivateRooms={setActivateRooms} ActivateRooms={ActivateRooms}
                                    roomsArr={roomsArr} setroomsArr={setroomsArr}
                                ></Rooms>
                            }

                        </div>

                        <div className={styles.column}>
                            <label className={styles.up}  >  מחיר</label>

                            <div className={styles.fromto}>
                                <input onChange={(e) => setpriceFrom(e.target.value)} className={styles.price} placeholder=' מ- '></input>
                                <input onChange={(e) => setpriceTo(e.target.value)} className={styles.price} placeholder='  עד-    '></input>
                            </div>




                        </div>

                        <button onClick={() => setadvancedSearchPanel(!advancedSearchPanel)} className={styles.advancedbutton}><div className={styles.title3}>חיפוש מתקדם</div>
                            <div className={styles.pluswrapper}>
                                <FontAwesomeIcon className={styles.iconplus} icon={faPlus}></FontAwesomeIcon>
                            </div></button>
                        <button onClick={() => {
                            // setresultAmount(1)
                            handleSearchType()
                            setActivateRooms(false)
                        }} className={styles.orangebutton}>
                            <div>חיפוש  </div>
                            <div className={styles.pluswrapper}>
                                <FontAwesomeIcon icon={faMagnifyingGlass}></FontAwesomeIcon>
                            </div></button>


                    </div>



                    {advancedSearchPanel && <AdvancedSearch setresults={setresults}
                        chosenCity={chosenCity} chosenStreet={chosenStreet} Search={Search}
                        priceFrom={priceFrom} priceTo={priceTo} roomNumberFrom={roomNumberFrom}
                        roomsNumberTo={roomsNumberTo}
                        setadvancedSearchPanel={setadvancedSearchPanel} setchosenCity={setchosenCity}
                        setchosenStreet={setchosenStreet} setpriceFrom={setpriceFrom}
                        setpriceTo={setpriceTo} setroomNumberFrom={setroomNumberFrom} setroomsNumberTo={setroomsNumberTo}
                        setestateKind={setestateKind}
                        results={results}
                        resultAmount={resultAmount} setresultAmount={setresultAmount} settransfer={settransfer}

                    ></AdvancedSearch>}

                </div>


                {!advancedSearchPanel && <Icons />}




                {!advancedSearchPanel && <Results results={results} setresults={setresults}  ></Results>}


            </div>
        </div>
    )
}

export default SearchRE