import React from 'react'
import styles from './EditPersonalInfo.module.scss'
function EditPersonalInfo({ loggedName, register, setValue, loggedfamilyname, loggedphone, loggedmail, handleSubmit, onSubmit }) {
    return (
        <div>
            <div className={styles.titleedit}  >עדכון פרטים</div>
            <form className={styles.form} >

                <div className={styles.shemwrapper}>
                    <div className={styles.shem} >  שם </div>
                </div>


                <div className={styles.innerform} >
                    <div className={styles.weight}  >פרטים אישיים</div>

                    <div className={styles.regrow}  >

                        <div className={styles.regcol} >
                            <label className={styles.weight} >שם פרטי</label>
                            <input placeholder={loggedName}  {...register("name")} onChange={(e) => setValue('name', e.target.value)} ></input>
                        </div>


                        <div className={styles.regcol} >
                            <label className={styles.weight} >שם משפחה</label>
                            <input placeholder={loggedfamilyname} {...register("familyname")} onChange={(e) => setValue('familyname', e.target.value)}  ></input>
                        </div>

                    </div>


                    <div className={styles.regrow}  >

                        <div className={styles.regcol} >
                            <label className={styles.weight} >  טלפון</label>
                            <input placeholder={loggedphone} {...register("phone")} onChange={(e) => setValue('phone', e.target.value)}   ></input>
                        </div>


                        <div className={styles.regcol} >
                            <label className={styles.weight} >  תאריך לידה</label>
                            <input></input>
                        </div>

                    </div>


                    <div className={styles.regrow}  >

                        <div className={styles.regcol} >
                            <label className={styles.weight}  >  מייל</label>
                            <input placeholder={loggedmail}  {...register("mail")} onChange={(e) => setValue('mail', e.target.value)}  ></input>
                        </div>


                        <div className={styles.liliacBG}   >
                            <div className={styles.liliactext} >אולי נצליח להפתיע אותך עם
                                ברכה ביום ההולדת!</div>
                        </div>

                    </div>

                    <div className={styles.regcol2} >
                        <label className={styles.weight2}  >    כתובת    </label>
                        <div className={styles.greytext}   >כדאי למלא את הכתובת, כדי שנוכל לחבר אותה אוטומטית בפעם הבאה שתפרסמו מודעה</div>
                    </div>

                    <div className={styles.regrow}  >

                        <div className={styles.regcol} >
                            <label className={styles.weight}  >  ישוב / עיר</label>
                            <input></input>
                        </div>

                        <div className={styles.regcol} >
                            <label className={styles.weight}  >    רחוב  </label>
                            <input className={styles.width2} ></input>
                        </div>

                        <div className={styles.regcol} >
                            <label className={styles.weight}  >    מספר בית  </label>
                            <input className={styles.width3}  ></input>
                        </div>



                    </div>


                    <div className={styles.finalbuttons} >
                        <div>ביטול</div>
                        <button className={styles.savebutton} onClick={handleSubmit(onSubmit)} > שמירה </button>


                    </div>

                </div>
            </form>
        </div>
    )
}

export default EditPersonalInfo