import { faLeaf } from '@fortawesome/free-solid-svg-icons'
import axios from 'axios'
import { saveLastSearch } from './utils'
let mainData = []
let currentLoggedName = ''

// basic search
export async function basicSearch(chosenCity, chosenStreet, Search, priceFrom, priceTo,
    roomNumberFrom, roomsNumberTo, setresults, resnum, loggedName) {
    currentLoggedName = loggedName
    let arr = []
    let arrNonMandatory = []
    let sql = `SELECT * FROM Object 
        LEFT JOIN NonMandatoryObject ON NonMandatoryObject.NM_id = Object.M_id
         LEFT  JOIN Pictures ON Pictures.PICS_id = Object.M_id WHERE    `

    let sqlNonMandatory = `SELECT * FROM NonMandatoryObject 
        JOIN Object ON Object.M_id = NonMandatoryObject.NM_id
        JOIN Pictures ON Pictures.PICS_id = NonMandatoryObject.NM_id WHERE
               `
    console.log('basic')

    let payload = {

        settlement: chosenCity,
        street: chosenStreet,
        kind: Search.REprops,

        pricefrom: priceFrom,
        roomsfrom: roomNumberFrom,
    }

    let payload_verification = {
        settlement: chosenCity,
        street: chosenStreet,
        kind: Search.REprops,

        pricefrom: priceFrom,
        priceto: priceTo,
        roomsfrom: roomNumberFrom,
        roomsto: roomsNumberTo
    }





    for (const i in payload) {

        if (payload[i] != null && payload[i] != '' && payload != undefined) {

            if (i == 'roomsfrom') {   // rooms from and rooms to (2-5)
                if (payload_verification.roomsto.length > 0) {
                    sql = sql + `(numberOfRooms > ? AND numberOfRooms < ?)`
                    arr.push(payload_verification.roomsfrom)
                    arr.push(payload_verification.roomsto)
                }
                if (payload_verification.roomsto.length == 0) {   // just rooms from (2+)
                    sql = sql + ` numberOfRooms > ?  `
                    arr.push(payload_verification.roomsfrom)
                }
            }


            else if (i == 'pricefrom') {                               //price 
                if (payload_verification.priceto != false) {  //price from to
                    sqlNonMandatory = sqlNonMandatory + `(price > ? AND price < ?)`
                    arrNonMandatory.push(payload_verification.pricefrom)
                    arrNonMandatory.push(payload_verification.priceto)
                }
                if (payload_verification.priceto == false) {
                    sqlNonMandatory = sqlNonMandatory + `price > ? `
                    arrNonMandatory.push(parseInt(payload_verification.pricefrom))
                }
            }


            else if (i == 'settlement' || i == 'kind' || i == 'street') {
                arr.push(payload[i].toString())
                sql = sql + ' ' + i + '= ? AND'
            }
        }
    }


    if (sql.slice((sql.length - 3), sql.length) == 'AND') sql = sql.slice(0, (sql.length - 3))
    let resNonMandatory = ''
    let res = ''
    if (arr.length > 0) { res = await axios.get(`http://localhost:8080/search`, { params: [arr, sql, 'placeholder', resnum] }) }

    if (arrNonMandatory.length > 0) {

        resNonMandatory = await axios.get(`http://localhost:8080/search`, { params: [arrNonMandatory, sqlNonMandatory, 'basicSearch', resnum] })
    }

    // setresults(res.data)
    //console.log(res.data)
    //console.log(resNonMandatory.data)
    mergeBasicResults(res.data, resNonMandatory.data, setresults)
}
//merge Basic Search results
function mergeBasicResults(mandatoryData, nonMandatoryData, setresults) {
    let finalResults = []



    if (nonMandatoryData != undefined && mandatoryData != undefined) {
        for (let i = 0; i < mandatoryData.length; i++) {
            for (let j = 0; j < nonMandatoryData.length; j++) {
                if (mandatoryData[i].M_id == nonMandatoryData[j].M_id) finalResults.push(mandatoryData[i])
            }
        }
        setresults(finalResults)
        mainData = finalResults

    }
    else if (nonMandatoryData == undefined && mandatoryData != undefined) {
        setresults(mandatoryData)
        mainData = mandatoryData
    }
    else if (mandatoryData == undefined && nonMandatoryData != undefined) {
        setresults(nonMandatoryData)
        mainData = nonMandatoryData
    }

}

//AdvancedSearch
export async function advancedSearch(data, loggedName) {
    let payload = data[0]
    let startDateModified
    if (parseInt(new Date(data[1]).getDate()) < 10) {
        startDateModified = new Date(data[1]).getFullYear() + '-' + '0' + (new Date(data[1]).getMonth() + 1) + '-' + '0' + new Date(data[1]).getDate()

    }
    else if (parseInt(new Date(data[1]).getDate()) > 10) {
        startDateModified = new Date(data[1]).getFullYear() + '-' + '0' + (new Date(data[1]).getMonth() + 1) + '-' + new Date(data[1]).getDate()

    }
    //let resnum = resultAmount
    let resnum = undefined
    payload.enteringDate = startDateModified
    let setresults = data[2]
    saveLastSearch(data, loggedName)
    ////
    let chosenCity = data[3]
    let chosenStreet = data[4]
    let Search = data[5]
    let priceFrom = data[6]
    let priceTo = data[7]
    let roomNumberFrom = data[8]
    let roomsNumberTo = data[9]

    basicSearch(chosenCity, chosenStreet, Search, priceFrom, priceTo,
        roomNumberFrom, roomsNumberTo, setresults, resnum)

    console.log('advanced')



    ///


    let res = ''
    let resNonMandatory = ''
    let resDescription = ''
    let arr = []
    let arrNonMandatory = []
    let arrDescription = []

    let sql = `SELECT * FROM Object 
        LEFT JOIN NonMandatoryObject ON NonMandatoryObject.NM_id = Object.M_id
         LEFT  JOIN Pictures ON Pictures.PICS_id = Object.M_id WHERE   `

    let sqlNonMandatory = `SELECT * FROM NonMandatoryObject 
        JOIN Object ON Object.M_id = NonMandatoryObject.NM_id
        JOIN Pictures ON Pictures.PICS_id = NonMandatoryObject.NM_id WHERE  `

    let sqlDescription = `SELECT * FROM NonMandatoryObject 
        JOIN Object ON Object.M_id = NonMandatoryObject.NM_id
        JOIN Pictures ON Pictures.PICS_id = NonMandatoryObject.NM_id WHERE  `

    for (const i in payload) {
        if (payload[i] != null && payload[i] != '' && payload != undefined) {

            if (i == 'bars' || i == 'disabled' || i == 'elevator' || i == 'furniture' || i == 'repaired' ||
                i == 'shelter' || i == 'mamad') {
                arrNonMandatory.push(1)
                sqlNonMandatory = sqlNonMandatory + ' ' + i + '= ? AND'
            }
            if (i == 'mazgan') {
                arrNonMandatory.push(1)
                arrNonMandatory.push(1)
                sqlNonMandatory = sqlNonMandatory + '(mazgan = ? OR tadiran = ? )  AND'

            }
            if (i == 'enteringDate') {
                arrNonMandatory.push(payload[i])
                sqlNonMandatory = sqlNonMandatory + ' DATE(enteringDate)   > DATE (?)   AND'

            }

            if (i == 'description') {
                arrDescription.push('%' + payload[i] + '%')
                sqlDescription = sqlDescription + `description like    ?   `

            }

            if (i == 'floorFrom') {                     // Mandatory Data
                if (payload.floorTo != '') {
                    sql = sql + ` (floorNumber > ? AND  floorNumber < ? ) AND`
                    arr.push(payload.floorFrom.trim())
                    arr.push(payload.floorTo.trim())
                }
                else if (payload.floorTo == '') {
                    sql = sql + ` floorNumber > ?  `
                    arr.push(payload.floorFrom.trim())
                }
            }

            if (i == 'sizeFrom') {                     // Mandatory Data
                if (payload.sizeTo != '') {
                    sql = sql + ` (quadrateMeterTotal > ? AND  quadrateMeterTotal < ? ) AND`
                    arr.push(payload.sizeFrom.trim())
                    arr.push(payload.sizeTo.trim())
                }
                else if (payload.sizeTo == '') {
                    sql = sql + ` quadrateMeterTotal > ?  `
                    arr.push(payload.sizeFrom.trim())
                }
            }
        }
    }



    if (sqlNonMandatory.slice((sqlNonMandatory.length - 3), sqlNonMandatory.length) == 'AND') sqlNonMandatory = sqlNonMandatory.slice(0, (sqlNonMandatory.length - 3))
    if (sql.slice((sql.length - 3), sql.length) == 'AND') sql = sql.slice(0, (sql.length - 3))


    if (arrNonMandatory.length > 0) // only props and date
    { resNonMandatory = await axios.get(`http://localhost:8080/search`, { params: [arrNonMandatory, sqlNonMandatory, 'advancedSearch', resnum] }) }

    if (arrDescription.length > 0) // only description
    { resDescription = await axios.get(`http://localhost:8080/search`, { params: [arrDescription, sqlDescription, 'placeholder', resnum] }) }


    if (arr.length > 0) // floor and square meter
    { res = await axios.get(`http://localhost:8080/search`, { params: [arr, sql, 'advancedSearchFloorsAndSquareMeter', resnum] }) }



    mergeAdvancedResults(res.data, resNonMandatory.data, resDescription.data, setresults)

}


//Merge advanced Search results
function mergeAdvancedResults(mandatoryData, nonMandatoryData, descriptionData, setresults) {
    let finalResults = []

    if (nonMandatoryData != undefined && mandatoryData != undefined && descriptionData != undefined) { // 3 categories non-empty

        for (let i = 0; i < mandatoryData.length; i++) {
            for (let j = 0; j < nonMandatoryData.length; j++) {
                for (let l = 0; l < descriptionData.length; l++) {
                    if ((mandatoryData[i].M_id == nonMandatoryData[j].M_id) && (mandatoryData[i].M_id == descriptionData[l].M_id)) {
                        finalResults.push(mandatoryData[i])
                    }
                }
            }
        }
        mergeBasicAndAdvancedResults(mainData, finalResults, setresults)
    }



    else if ((nonMandatoryData == undefined && descriptionData == undefined) && mandatoryData != undefined) { // 1 category non empty
        mergeBasicAndAdvancedResults(mainData, mandatoryData, setresults)
    }


    else if (((mandatoryData == undefined || mandatoryData.length == 0) && descriptionData == undefined) && nonMandatoryData != undefined) { // 1 category non empty
        mergeBasicAndAdvancedResults(mainData, nonMandatoryData, setresults)
    }


    else if (((mandatoryData == undefined || mandatoryData.length == 0) && nonMandatoryData == undefined) && descriptionData != undefined) { // 1 category non empty
        mergeBasicAndAdvancedResults(mainData, descriptionData, setresults)
    }


    else {                                                                                                      // 2 categories non empty
        scanTables(mandatoryData, nonMandatoryData, descriptionData, finalResults, setresults, mergeBasicAndAdvancedResults)
        scanTables(mandatoryData, descriptionData, nonMandatoryData, finalResults, setresults, mergeBasicAndAdvancedResults)
        scanTables(nonMandatoryData, descriptionData, mandatoryData, finalResults, setresults, mergeBasicAndAdvancedResults)
    }

}


//Merge advanced Search results
function scanTables(firstData, secondData, thirdData, finalResults, setresults) {
    if ((firstData != undefined && secondData != undefined) && thirdData == undefined) {
        for (let i = 0; i < firstData.length; i++) {
            for (let j = 0; j < secondData.length; j++) {
                if (firstData[i].M_id == secondData[j].M_id) finalResults.push(firstData[i])
            }

        }
    }
    mergeBasicAndAdvancedResults(mainData, finalResults, setresults)
}
//Merge basic search results with advanced search results
function mergeBasicAndAdvancedResults(basicData, advancedData, setresults) {
    let mergedFinalResults = []
    for (let i = 0; i < basicData.length; i++) {
        for (let j = 0; j < advancedData.length; j++) {
            if (basicData[i].M_id == advancedData[j].M_id) mergedFinalResults.push(basicData[i])
        }

    }
    setresults(mergedFinalResults)
}





