import React, { useState } from 'react'
import axios from "axios"

import { Link, useLocation, useNavigate } from 'react-router-dom'
import { useForm } from "react-hook-form";
import { useGlobalContext } from '../redux/actions'
import styles from './Login.module.scss'
import apple from './../components/pics/apple.png'
import google from './../components/pics/google.png'
import icon_alert from './../components/pics/icon_alert.png'
import icon_chat from './../components/pics/icon_chat.png'
import icon_profile from './../components/pics/icon_profile.png'
import icon_publish from './../components/pics/icon_publish.png'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEye } from '@fortawesome/free-solid-svg-icons';
import { faEyeSlash } from '@fortawesome/free-solid-svg-icons';
import { reset } from '../utils.js'
import Registration from './Registration';
import { loginValidation } from './loginUtils'
function LogReg() {
    const { register, handleSubmit, watch, formState: { errors } } = useForm();
    const [mail, setmail] = useState(false)
    const [mail2, setmail2] = useState(false)
    const [password, setpassword] = useState(false)
    const location = useLocation()
    const navigate = useNavigate()
    const [loginC, setLogin] = useState(true)
    const [registerC, setRegister] = useState(false)
    const [viewPass, setviewPass] = useState(false)
    const [PasswordMismatch, setPasswordMismatch] = useState(false)
    const [NoFoundUser, setNoFoundUser] = useState(false)
    const [registration_, setRegistration] = useState(false)
    const { login } = useGlobalContext()
    const typedMail = watch("mail", "")
    const typedPassword = watch("password", "")
    const typedPasswordValidation = watch("passwordValidation", "")

    async function onSubmit(data, event) {
        loginValidation(data, event, navigate, typedMail, typedPassword, typedPasswordValidation, setPasswordMismatch,
            setRegistration, setpassword, setmail, setmail2, login, setNoFoundUser)
    }

    return (

        <div>
            <div className={styles.mainBG}>

                <div><  img className={styles.imgpadding} src='https://assets.yad2.co.il/personal/images/general/new_logo_orange.png'></img></div>  {/* navbar img*/}

                <div className={styles.flex}  >  {/* main div*/}

                    <div className={styles.whiteBG}  >{/* right div*/}






                        <div className={styles.flexcolumn} id='around-the-form'>
                            {loginC && <div className={styles.title}>היי, טוב לראות אותך</div>}
                            {registerC && <div className={styles.title}> היי, נעים להכיר     </div>}

                            <form onChange={() => reset(typedMail, setmail, setmail2, typedPassword, setpassword)} className={styles.column} >
                                <label className={styles.title2}> מייל </label>

                                <input className={styles.noBG} placeholder='yourmail@email.co.il'  {...register('mail')}></input>

                                {mail && <div className={styles.warning} >לא לשכוח את המייל</div>}
                                {(!mail && mail2) && <div className={styles.warning} >משהו לא תקין במייל, אולי התפספס @?</div>}

                                <label className={styles.title2}> סיסמה </label>
                                <div className={styles.divright} >
                                    {!viewPass && <FontAwesomeIcon onClick={() => setviewPass(!viewPass)} className={styles.eye} icon={faEye} />}
                                    {viewPass && <FontAwesomeIcon onClick={() => setviewPass(!viewPass)} className={styles.eye} icon={faEyeSlash} />}

                                    <input type={!viewPass ? 'password' : 'tel'} className={styles.noBG} placeholder='הקלדת סיסמה' {...register('password')}></input></div>
                                {password && <div className={styles.warning}>לא לשכוח להזין סיסמה</div>}
                                {NoFoundUser && <div className={styles.warning}> משתמש זה לא קיים במערכת  </div>}
                                {registerC &&

                                    <div className={styles.flexcolumn} >
                                        <label className={styles.title2}> אימות סיסמה </label>
                                        <div className={styles.divright}>

                                            {!viewPass && <FontAwesomeIcon onClick={() => setviewPass(!viewPass)} className={styles.eye2} icon={faEye} />}
                                            {viewPass && <FontAwesomeIcon onClick={() => setviewPass(!viewPass)} className={styles.eye2} icon={faEyeSlash} />}
                                            <input type={!viewPass ? 'password' : 'tel'} className={styles.noBG} placeholder='הקלדת סיסמה' {...register('passwordValidation')}></input></div>
                                        {PasswordMismatch && <div className={styles.warning}>נבקש שהסיסמה תכלול אותיות באנגלית וספרות</div>}



                                    </div>
                                }

                            </form>

                            <div className={styles.orangetitle}>שכחתי סיסמה</div>

                            <button onClick={handleSubmit(onSubmit)} className={styles.btnOrange}>{registerC ? 'הרשמה' : 'התחברות'}</button>

                            <div className={styles.textandbars}>{/* grey line and text  */}
                                <div className={styles.horizontalbar}></div>
                                <div className={styles.titlehorizontal}>או התחברו דרך</div>
                                <div className={styles.horizontalbar}></div>
                            </div>

                            <div className={styles.flex2}>{/* apple and android */}
                                <div className={styles.greybutton}>
                                    <img className={styles.google} src={google}></img>
                                    <div>גוגל</div>

                                </div>

                                <div className={styles.greybutton}>
                                    <img className={styles.apple} src={apple}></img>
                                    <div>אפל</div>

                                </div>
                            </div>


                            {loginC &&
                                <div className={styles.title3} >אין לך חשבון?<div onClick={() => {
                                    setRegister(true)
                                    setLogin(false)

                                }
                                } className={styles.title2orange} >להרשמה</div></div>}

                            {registerC &&
                                <div className={styles.title3} > כבר יש לך חשבון ?   <div onClick={() => {
                                    setLogin(true)
                                    setRegister(false)
                                }} className={styles.title2orange} >להתחברות</div></div>
                            }
                        </div>

                    </div>





                    <div className={styles.margin1}  >{/* left div*/}

                        <div className={styles.largetitle} >לקנות מהר, למכור מהר.</div>
                        <div className={styles.largetitle} >ויש לנו אחלה כלים לזה.</div>

                        <div className={styles.flex}>{/* left div pics*/}
                            <div className={styles.column}>
                                <img className={styles.imgsize} src={icon_alert}></img>
                                <div className={styles.title2}>התראות</div>
                            </div>

                            <div className={styles.column}>
                                <img className={styles.imgsize} src={icon_chat}></img>
                                <div className={styles.title2}>צ׳אט</div>
                            </div>

                            <div className={styles.column}>
                                <img className={styles.imgsize} src={icon_profile}></img>
                                <div className={styles.title2}>פרסום מודעה</div>
                            </div>

                            <div className={styles.column}>
                                <img className={styles.imgsize} src={icon_publish}></img>
                                <div className={styles.title2}>אזור אישי</div>
                            </div>

                        </div>

                    </div>



                </div>


            </div>
        </div>

    )
}

export default LogReg