import React from 'react'
import styles from './PersonalInfoNotLogged.module.scss'
import { Link, useNavigate } from 'react-router-dom'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowRightArrowLeft } from '@fortawesome/free-solid-svg-icons'
import { faClockRotateLeft } from '@fortawesome/free-solid-svg-icons'
function PersonalInfoNotLogged({ setactivateprivateAreaInfo, setactivateprivateAreaInfominus }) {
    return (
        <div className={styles.personalInfo}
            onMouseLeave={() => {
                setactivateprivateAreaInfo(false)
                setactivateprivateAreaInfominus(false)
            }}
        >

            <div className={styles.privatearea} >
                <FontAwesomeIcon className={styles.iconmargin} icon={faArrowRightArrowLeft} /> <div > השוואת רכבים</div>
            </div>

            <Link className={styles.Linkdecoration} to='./lastSearches' > <div className={styles.privatearea}  >
                <FontAwesomeIcon className={styles.iconmargin} icon={faClockRotateLeft} /> <div >   חיפושים אחרונים</div>
            </div>
            </Link>

        </div>
    )
}

export default PersonalInfoNotLogged