import React, { useState, useEffect } from 'react'
import styles from './Rooms.module.scss'
import { useGlobalContext } from '../../../redux/actions'

function Rooms({ setroomNumberFrom, setroomsNumberTo, setroomsmin, setroomsmax, ActivateRooms, roomsArr, setroomsArr }) {
    const { Nrooms } = useGlobalContext()
    const [roomsAmount, setroomsAmount] = useState(10)
    const [activateroomcount, setactivateroomcount] = useState(true)
    useEffect(() => {
        for (let i = 0; i < (10 - roomsAmount); i++) {
            roomsArr.push(i)
        }
    }, [roomsAmount])

    function handleSubmit(e) {
        if (e.target.id === 'from') {
            setroomNumberFrom(e.target.value)
            Nrooms([e.target.value, e.target.id])
            setroomsmin(e.target.value)
            setactivateroomcount(!activateroomcount)
            setroomsAmount(e.target.value)
        }
        else if (e.target.id === 'to') {
            setroomsNumberTo(e.target.value)
            Nrooms([e.target.value, e.target.id])
            setroomsmax(e.target.value.toString())

        }

    }




    return (
        <div className={styles.mainwrapper}>
            <div className={styles.wrapper}>
                <div>

                    <select className={styles.unit} id='from' onChange={(e) => {
                        handleSubmit(e)

                    }} placeholder='מ-' >

                        <option>1</option>
                        <option>1.5</option>
                        <option>2</option>
                        <option>2.5</option>
                        <option>3</option>
                        <option>3.5</option>
                        <option>4</option>
                        <option>4.5</option>
                        <option>5</option>
                        <option>5.5</option>
                        <option>6</option>
                        <option>6.5</option>
                        <option>7</option>
                        <option>7.5</option>
                        <option>8</option>
                        <option>8.5</option>
                        <option>9</option>
                        <option>9.5</option>
                        <option>10</option>


                    </select>

                    <select className={styles.unit}
                        onChange={(e) => { handleSubmit(e) }}
                        id='to' placeholder='עד-' >

                        {roomsArr.map((r) => <option>{parseInt(r) + parseInt(roomsAmount) + 1}</option>)}




                    </select>

                </div>
            </div>
        </div>
    )
}

export default Rooms