import React from 'react'
import styles from './GreyFav.module.scss'
import { useGlobalContext } from '../../redux/actions'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faShekelSign } from '@fortawesome/free-solid-svg-icons'
import { faHeart } from '@fortawesome/free-solid-svg-icons'
import { faChevronLeft } from '@fortawesome/free-solid-svg-icons'
import { faChevronRight } from '@fortawesome/free-solid-svg-icons'
import { faPhone } from '@fortawesome/free-solid-svg-icons'
import { faXmark } from '@fortawesome/free-solid-svg-icons'
import { faCheck } from '@fortawesome/free-solid-svg-icons'
import { faBell } from '@fortawesome/free-solid-svg-icons'
import { slideImage, showPhoneNum, validateInput } from './navbarUtils.js'
import { useForm } from "react-hook-form";
import sidebar3 from '../pics/sidebar3.png'
function GreyFav({ f, setcurrentImg, setimgcounter, settotalimgcounter, setmodaladdInfo, currentImg, totalimgcounter, imgcounter, setmailmodal, index }) {

    return (

        <div className={styles.modal}>


            <div className={styles.toresults}>
                <div onClick={(e) => {
                    e.stopPropagation()
                    setmodaladdInfo(false)
                }} className={styles.text3}>חזרה לתוצאות חיפוש</div>
                <FontAwesomeIcon icon={faChevronLeft}></FontAwesomeIcon>
            </div>


            <div className={styles.modalInnerBox}>



                <div className={styles.width2}   >  {/* grey area and picture inside */}

                    <div className={styles.greyarea}>
                        <FontAwesomeIcon className={styles.orangeheart2} icon={faHeart}  ></FontAwesomeIcon>
                        <div className={styles.chevrons}  >
                            {/* images slide show  */}
                            <FontAwesomeIcon id='right' onClick={(e) => {
                                e.stopPropagation()
                                slideImage(e, f, setcurrentImg, setimgcounter, settotalimgcounter)
                            }} icon={faChevronRight} className={styles.chevron} ></FontAwesomeIcon>

                            <FontAwesomeIcon id='left' onClick={(e) => {
                                e.stopPropagation()
                                slideImage(e, f, setcurrentImg, setimgcounter, settotalimgcounter)
                            }} icon={faChevronLeft} className={styles.chevron} ></FontAwesomeIcon>
                        </div>

                        <img className={styles.imgingrey} src={currentImg} />
                        <div className={styles.photocount} >{`${totalimgcounter} / ${imgcounter + 1} `}</div>
                    </div>

                </div>

                <div className={styles.mainwrapper} id='mainwrapper'>  {/* the second part, information */}

                    <div className={styles.flexinfo}  >
                        <div>{`  דיור למכירה ב${f.settlement}  `}</div>
                        <div > עודכן היום </div>
                    </div>
                    <div className={styles.innerwrapper} id='innerwrapper'>

                        <div id='adresswrapper'>
                            <div className={styles.flexcolstreet}  >
                                <div>{f.street}</div>
                                <div>{f.houseNumber}</div>
                            </div>

                            <div className={styles.flexcol2}  >
                                <div>{f.settlement}</div>
                                <div className={styles.flat}  >{f.kind}</div>
                            </div>
                        </div>
                        <div className={styles.flexrow}  >

                            <div className={styles.flexcol3} >
                                <div>{f.numberOfRooms}</div>
                                <div className={styles.undernumber} >חדרים</div>
                            </div>

                            <div className={styles.flexcol3}>
                                <div>{f.floorNumber}</div>
                                <div className={styles.undernumber} >קומה</div>
                            </div>

                            <div className={styles.flexcol3}>
                                <div>{f.quadrateMeterTotal}</div>
                                <div className={styles.undernumber} >מ"ר</div>
                            </div>

                        </div>

                        <div className={styles.niswrapper} >
                            <div>{f.price}</div>
                            <FontAwesomeIcon className={styles.nissign} icon={faShekelSign} ></FontAwesomeIcon>
                        </div>



                    </div>
                    <div className={styles.geves} >    <div>לכל סוגי עבודות הגבס</div><FontAwesomeIcon icon={faChevronLeft}   > </FontAwesomeIcon></div>
                </div>



            </div>

            <div className={styles.innerbox2} >
                <div className={styles.whiteinnerbox} id='whiteinnerbox'>

                    <div className={styles.megawrapper} >
                        <div className={styles.rightarea} id='right fat area'>
                            <div className={styles.titleRE}  >על הנכס</div>
                            <div className={styles.description}  >  {f.description ? f.description : 'ללא פירוט'}</div>

                            <div className={styles.uppersplit} >

                                <div className={styles.upper} >

                                    <div className={styles.flexrows} >

                                        <div className={styles.flexcolumns} >
                                            <div className={styles.innerflex}>
                                                <div className={styles.propsdescription} >מצב הנכס</div><div className={styles.propsdescription2} >{f.condition}</div>
                                            </div>
                                            <div className={styles.bar}></div>
                                            <div className={styles.innerflex}>
                                                <div className={styles.propsdescription} >  קומות בבנין</div><div className={styles.propsdescription2} >{f.totalFloorNumber}</div>
                                            </div>
                                            <div className={styles.bar}></div>

                                            <div className={styles.innerflex}>
                                                <div className={styles.propsdescription} >  חניות</div><div className={styles.propsdescription2} >{f.numberOfParkings}</div>
                                            </div>
                                            <div className={styles.bar}></div>

                                        </div>

                                        <div className={styles.flexcolumns} >

                                        </div>

                                    </div>
                                </div>

                                <div className={styles.lower} >

                                    <div className={styles.flexrows} >

                                        <div className={styles.flexcolumns} >
                                            <div className={styles.innerflex}>
                                                <div className={styles.propsdescription} >  תאריך כניסה</div><div className={styles.propsdescription2} >{f.enteringDate}</div>
                                            </div>
                                            <div className={styles.bar}></div>

                                            <div className={styles.innerflex}>
                                                <div className={styles.propsdescription} >    מרפסות</div><div className={styles.propsdescription2} >{f.numberOfBalconies}</div>
                                            </div>
                                            <div className={styles.bar}></div>


                                        </div>



                                    </div>

                                </div>
                            </div>

                        </div>
                        <div id='secondwhite' className={styles.secondwhite} >
                            <div className={styles.titleswrapper} >
                                <div className={styles.title4} >מה יש בנכס?</div>  <div className={styles.orangetext2}  >{`פרויקטים חדשים במבצעים מיוחדים>`}</div>
                                <div className={styles.iconwrapper1} id='iconswrapper'>
                                    <div className={styles.quarterwidth} id='column1'>

                                        <div className={styles.regularflex}>
                                            {f.mazgan === 0 && <div className={styles.notactive}  >x</div>}
                                            {f.mazgan === 1 && <FontAwesomeIcon className={styles.active} icon={faCheck}  ></FontAwesomeIcon>}
                                            <div className={f.mazgan === 0 ? styles.notactivetext : styles.activetext} >מיזוג</div>
                                        </div>

                                        <div className={styles.regularflex}>
                                            {f.mamad === 0 && <div className={styles.notactive}  >x</div>}
                                            {f.mamad === 1 && <FontAwesomeIcon className={styles.active} icon={faCheck}  ></FontAwesomeIcon>}
                                            <div className={f.mamad === 0 ? styles.notactivetext : styles.activetext} >מחסן</div>
                                        </div>
                                        <div className={styles.regularflex}>
                                            {f.shelter === 0 && <div className={styles.notactive}  >x</div>}
                                            {f.shelter === 1 && <FontAwesomeIcon className={styles.active} icon={faCheck}  ></FontAwesomeIcon>}
                                            <div className={f.shelter === 0 ? styles.notactivetext : styles.activetext} >ממ"ד</div>
                                        </div>
                                    </div>
                                    <div className={styles.quarterwidth} id='column2' >

                                        <div className={styles.regularflex}>
                                            {f.furniture === 0 && <div className={styles.notactive}  >x</div>}
                                            {f.furniture === 1 && <FontAwesomeIcon className={styles.active} icon={faCheck}  ></FontAwesomeIcon>}
                                            <div className={f.furniture === 0 ? styles.notactivetext : styles.activetext} >ריהוט</div>
                                        </div>

                                        <div className={styles.regularflex}>
                                            {f.disabled === 0 && <div className={styles.notactive}  >x</div>}
                                            {f.disabled === 1 && <FontAwesomeIcon className={styles.active} icon={faCheck}  ></FontAwesomeIcon>}
                                            <div className={f.disabled === 0 ? styles.notactivetext : styles.activetext} >גישה לנכים</div>
                                        </div>
                                        <div className={styles.regularflex}>
                                            {f.elevator === 0 && <div className={styles.notactive}  >x</div>}
                                            {f.elevator === 1 && <FontAwesomeIcon className={styles.active} icon={faCheck}  ></FontAwesomeIcon>}
                                            <div className={f.elevator === 0 ? styles.notactivetext : styles.activetext} >  מעלית </div>
                                        </div>

                                    </div>
                                    <div className={styles.quarterwidth} id='column3'  >
                                        <div className={styles.regularflex}>
                                            {f.tadiran === 0 && <div className={styles.notactive}  >x</div>}
                                            {f.tadiran === 1 && <FontAwesomeIcon className={styles.active} icon={faCheck}  ></FontAwesomeIcon>}
                                            <div className={f.tadiran === 0 ? styles.notactivetext : styles.activetext} >מזגן תדיראן</div>
                                        </div>

                                        <div className={styles.regularflex}>
                                            {f.repaired === 0 && <div className={styles.notactive}  >x</div>}
                                            {f.repaired === 1 && <FontAwesomeIcon className={styles.active} icon={faCheck}  ></FontAwesomeIcon>}
                                            <div className={f.repaired === 0 ? styles.notactivetext : styles.activetext} >  משופצת</div>
                                        </div>
                                        <div className={styles.regularflex}>
                                            {f.citchen === 0 && <div className={styles.notactive}  >x</div>}
                                            {f.citchen === 1 && <FontAwesomeIcon className={styles.active} icon={faCheck}  ></FontAwesomeIcon>}
                                            <div className={f.citchen === 0 ? styles.notactivetext : styles.activetext} >  מטבח </div>
                                        </div>
                                    </div>
                                    <div className={styles.quarterwidth} id='column4' >
                                        <div className={styles.regularflex}>
                                            {f.boiler === 0 && <div className={styles.notactive}  >x</div>}
                                            {f.boiler === 1 && <FontAwesomeIcon className={styles.active} icon={faCheck}  ></FontAwesomeIcon>}
                                            <div className={f.boiler === 0 ? styles.notactivetext : styles.activetext} >  דוד שמש</div>
                                        </div>

                                        <div className={styles.regularflex}>
                                            {f.bars === 0 && <div className={styles.notactive}  >x</div>}
                                            {f.bars === 1 && <FontAwesomeIcon className={styles.active} icon={faCheck}  ></FontAwesomeIcon>}
                                            <div className={f.bars === 0 ? styles.notactivetext : styles.activetext}  >  סורגים</div>
                                        </div>
                                        <div className={styles.regularflex}>
                                            {f.mazgan === 0 && <div className={styles.notactive}  >x</div>}
                                            {f.mazgan === 1 && <FontAwesomeIcon className={styles.active} icon={faCheck}  ></FontAwesomeIcon>}
                                            <div className={f.mazgan === 0 ? styles.notactivetext : styles.activetext}  >  יחידת דיור </div>
                                        </div>
                                    </div>



                                </div>

                            </div>


                        </div>
                        <div className={styles.end} >
                            <FontAwesomeIcon className={styles.margin3} icon={faBell} ></FontAwesomeIcon>
                            <div>{`    רוצה לדעת כמה שווה הנכס? >    `}</div>
                        </div>
                    </div>
                    <div className={styles.leftarea} id='left slim area'>

                        <div>  <div className={styles.advertiser} >מפרסם/ת המודעה</div>
                            <div className={styles.name}  >{f.contactName != '' ? f.contactName : 'לא צוין שם'}</div></div>

                        <div id={index + 1000} onClick={(e) => {
                            e.stopPropagation()
                            showPhoneNum(index, e)
                        }} className={styles.phone} >
                            <FontAwesomeIcon icon={faPhone} ></FontAwesomeIcon>
                            <div className={styles.text3} >הצגת מספר טלפון</div>
                        </div>

                        <div className={styles.actualphonenumber} id={index + 1050}>{f.phone != null ? f.phone : 'לא צוין מספר טלפון'}</div>



                        <div className={styles.mail} onClick={() => setmailmodal(true)}  >שליחת דוא"ל למפרסם</div>

                        <img className={styles.sidebar3} src={sidebar3} ></img>
                    </div>

                </div>
            </div>

            <div>

            </div>

        </div>




    )
}

export default GreyFav