
import axios from "axios"

export async function loginValidation(data, event, navigate, typedMail, typedPassword, typedPasswordValidation, setPasswordMismatch,
    setRegistration, setpassword, setmail, setmail2, login, setNoFoundUser) {
    if (event.target.innerHTML === "התחברות") {    // login
        let res = await axios.post(`http://localhost:8080/login`, data)
        if (res.data.length === 0) setNoFoundUser(true)

        else if (res.data.length === 1) {
            document.getElementById('navbar').style.display = 'flex'
            login({ name: res.data[0].name, family: res.data[0].family, mail: res.data[0].mail, loggedphone: res.data[0].mobilephone })
            navigate('..')
        }



    }

    else if (event.target.innerHTML === "הרשמה") {      //register

        if (typedMail.length === 0 || typedPassword.length === 0 || !(typedMail.includes("@"))) {
            if (typedMail.length === 0) setmail(true)
            if (!(typedMail.includes("@"))) setmail2(true)
            if (typedPassword.length === 0) setpassword(true)
        }
        if (typedPassword !== typedPasswordValidation) setPasswordMismatch(true)

        else {                                                          // navigate to <Registration>
            document.getElementById('navbar').style.display = 'none'
            setRegistration(true)
            navigate('../register', { state: { data } })


        }
    }


}
