import React from 'react'
import styles from './FindCity.module.scss'
function FindCity({ city, setadressValue, handleAdress, setCity, setStreet, setadress, setchosenCity, setchosenStreet, street }) {
    return (


        <div className={styles.adress}>

            <div>
                {city.length > 0 && <div className={styles.title1} >עיר</div>}
                {city.length > 0 &&
                    city.map((c) =>

                        <div id='city' onClick={(e) => {
                            setadressValue(e.target.innerText.split('\n')[0])
                            handleAdress(e, setCity, setStreet, setadress, setchosenCity, setchosenStreet)
                        }}


                            className={styles.flexrow}>
                            <div className={styles.text}   >{c}</div>
                            <div className={styles.text2}  >עיר</div>
                        </div>
                    )

                }
            </div>



            <div>
                {street.length > 0 && <div className={styles.title1} >רחוב</div>}
                {street.length > 0 &&

                    street.map((s) =>
                        <div id='street' onClick={(e) => {
                            setadressValue(e.target.innerText)
                            handleAdress(e, setCity, setStreet, setadress, setchosenCity, setchosenStreet)
                        }}
                            className={styles.flexrow}>
                            <div id='street' className={styles.text}  >{s}</div>
                            <div className={styles.text2}  >רחוב</div>
                        </div>
                    )

                }
            </div>

        </div>
    )
}

export default FindCity