


// filtering out no - price and no - image adds
export function filterResults(results, setresults, checkboxprice, setfilter, checkboximg, setGreyshadow1, setcheckboximg, setcheckboxprice) {
    if (checkboxprice && !checkboximg) {
        let arr = []
        for (let i = 0; i < results.length; i++) {
            if (results[i].price != undefined && results[i].price != null && results[i].price != '' && results[i].price != 0) {
                arr.push(results[i])
            }

        }
        setresults(arr)
    }
    else if (checkboximg && !checkboxprice) {
        let arrImg = []
        for (let i = 0; i < results.length; i++) {
            if (results[i].main != null && results[i].main != '' && results[i].main != undefined) arrImg.push(results[i])
            if (results[i].one != null && results[i].one != '' && results[i].one != undefined) arrImg.push(results[i])
            if (results[i].two != null && results[i].two != '' && results[i].two != undefined) arrImg.push(results[i])
            if (results[i].three != null && results[i].three != '' && results[i].three != undefined) arrImg.push(results[i])

        }
        setresults(arrImg)

    }

    else if (checkboximg && checkboxprice) {
        let priceandImgArr = []
        for (let i = 0; i < results.length; i++) {
            if ((results[i].main != null && results[i].main != '' && results[i].main != undefined) &&
                (results[i].price != undefined && results[i].price != null && results[i].price != '' && results[i].price != 0)) priceandImgArr.push(results[i])

            if ((results[i].one != null && results[i].one != '' && results[i].one != undefined) &&
                (results[i].price != undefined && results[i].price != null && results[i].price != '' && results[i].price != 0)) priceandImgArr.push(results[i])

            if ((results[i].two != null && results[i].two != '' && results[i].two != undefined) &&
                (results[i].price != undefined && results[i].price != null && results[i].price != '' && results[i].price != 0)) priceandImgArr.push(results[i])

            if ((results[i].three != null && results[i].three != '' && results[i].three != undefined) &&
                (results[i].price != undefined && results[i].price != null && results[i].price != '' && results[i].price != 0)) priceandImgArr.push(results[i])

        }
        setresults(priceandImgArr)
    }

    setfilter(false)
    setGreyshadow1(false)
    setcheckboximg(false)
    setcheckboxprice(false)

}
// activate orange color
export function activateInnerOrange(data, results, setresults) {
    document.getElementById(data.slice(0, (data.length - 1))).style.height = '10px'
    document.getElementById(data).style.borderColor = '#f77100'
    orderResults(data, results, setresults)
}


// ordering adds by date and price
function orderResults(data, results, setresults) {
    let priceUpData = []

    if (data === 'priceup_' || data === 'pricedown_') {
        for (let j = 0; j < results.length; j++) { if (results[j].price != null && results[j].price != undefined && results[j].price != '') priceUpData.push(results[j]) }

        if (data === 'priceup_') {
            priceUpData.sort((p1, p2) => (parseInt(p1.price) < parseInt(p2.price)) ? -1 : (parseInt(p1.price) > parseInt(p2.price)) ? 1 : 0)
            document.getElementById('pricedown').style.height = '0px'
            document.getElementById('pricedown_').style.borderColor = '#999797'
            document.getElementById('date').style.height = '0px'
            document.getElementById('date_').style.borderColor = '#999797'
        }
        if (data === 'pricedown_') {
            priceUpData.sort((p1, p2) => (parseInt(p1.price) < parseInt(p2.price)) ? 1 : (parseInt(p1.price) > parseInt(p2.price)) ? -1 : 0)
            document.getElementById('priceup').style.height = '0px'
            document.getElementById('priceup_').style.borderColor = '#999797'
            document.getElementById('date').style.height = '0px'
            document.getElementById('date_').style.borderColor = '#999797'
        }
        setresults(priceUpData)
    }
    if (data === 'date_') {
        for (let j = 0; j < results.length; j++) { if (results[j].enteringDate != null && results[j].enteringDate != undefined && results[j].enteringDate != '') priceUpData.push(results[j]) }
        document.getElementById('priceup').style.height = '0px'
        document.getElementById('priceup_').style.borderColor = '#999797'
        document.getElementById('pricedown').style.height = '0px'
        document.getElementById('pricedown_').style.borderColor = '#999797'

        priceUpData.sort((p1, p2) => (new Date(p1.enteringDate).getTime() < new Date(p2.enteringDate).getTime()) ? -1 :
            (new Date(p1.enteringDate).getTime() > new Date(p2.enteringDate).getTime()) ? 1 : 0)

        setresults(priceUpData)

    }

}

// Ordering adds by payment path
export function orderByPaymentPath(data, setresults) {
    let sortedProducts = data.sort(
        (p1, p2) => (p1.paymentPath < p2.paymentPath) ? 1 : (p1.paymentPath > p2.paymentPath) ? -1 : 0);
    setresults(data)
}

