export function reset(typedMail, setmail, setmail2, typedPassword, setpassword) {
    if (typedMail.length > 0) setmail(false)
    if (typedMail.includes("@")) setmail2(false)
    if (typedPassword.length > 0) setpassword(false)

}


export function heartInfo(numberOfFavoriteAdds, sethoveringoverheart, setemptyAddInfo, setactivateprivateAreaInfominus, setactivateprivateAreaInfo) {
    numberOfFavoriteAdds > 0 ? sethoveringoverheart(true) : setemptyAddInfo(true)
    setactivateprivateAreaInfominus(false)
    setactivateprivateAreaInfo(false)
}