import React from 'react'
import proffesional_main from '../pics/proffesional_main.png'
import Pets_main from '../pics/Pets_main.png'
import RE_main from '../pics/RE_main.png'
import seconddHand from '../pics/seconddHand.png'
import Wanted_main from '../pics/Wanted_main.png'
import Vehicle_main from '../pics/Vehicle_main.png'
import Bussiness_main from '../pics/Bussiness_main.png'

import styles from './HomePageSemiFooter.module.scss'

function HomePageSemiFooter() {
    return (
        <div className={styles.iconwrapper} >

            <div className={styles.flexcolumn}>
                <img className={styles.iconborder} src={proffesional_main} ></img>
                <span className={styles.icontext}>  בעלי מקצוע</span></div>

            <div className={styles.flexcolumn}>
                <img className={styles.iconborder} src={Pets_main} ></img>
                <span className={styles.icontext}> חיות מחמד </span></div>

            <div className={styles.flexcolumn}>
                <img className={styles.iconborder} src={RE_main} ></img>
                <span className={styles.icontext}>   נדל"ן </span></div>

            <div className={styles.flexcolumn}>
                <img className={styles.iconborder} src={seconddHand} ></img>
                <span className={styles.icontext}>   יד שניה </span></div>

            <div className={styles.flexcolumn}>
                <img className={styles.iconborder} src={Wanted_main} ></img>
                <span className={styles.icontext}>   דרושים </span></div>


            <div className={styles.flexcolumn}>
                <img className={styles.iconborder2} src={Bussiness_main} ></img>
                <span className={styles.icontext}>   עסקים למכירה  </span></div>


            <div className={styles.flexcolumn}>
                <img className={styles.iconborder} src={Vehicle_main} ></img>
                <span className={styles.icontext}>   רכב </span></div>


        </div>

    )
}

export default HomePageSemiFooter