import React from 'react'
import styles from './SideBar.module.scss'
import { useGlobalContext } from '../../redux/actions'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEnvelope, faPencil, faMagnifyingGlass, faHeart, faPerson, faPhone, faChevronDown } from '@fortawesome/free-solid-svg-icons'
import { Link, useNavigate } from 'react-router-dom'

function SideBar({ messages, setmessages, editinfo, setEditinfo }) {
    const { loggedName, loggedfamilyname, loggedmail } = useGlobalContext()
    const navigate = useNavigate()
    return (
        <div className={styles.left} >

            <div>
                <div className={styles.person}  >{`${loggedName.slice(0, 1).toUpperCase()}${loggedfamilyname.slice(0, 1).toUpperCase()}`}</div>
                <div className={styles.name} ><div className={styles.padding1} >{loggedName}</div><div>{loggedfamilyname}</div></div>
                <div className={styles.mail}  >{loggedmail}</div>
            </div>

            <div className={styles.margins} >
                <div className={messages ? styles.iconwrapperdeep : styles.iconwrapper}
                    onClick={() => {
                        setEditinfo(false)
                        setmessages(true)
                    }}
                ><FontAwesomeIcon className={styles.iconssize} icon={faEnvelope} />
                    <div>  המודעות שלי    </div></div>

                <div className={editinfo ? styles.iconwrapperdeep : styles.iconwrapper}
                    onClick={() => {
                        setEditinfo(true)
                        setmessages(false)
                    }} >
                    <FontAwesomeIcon className={styles.iconssize} icon={faPencil} /><div>
                    </div>עדכון פרטים</div>

                <div onClick={() => navigate('../favPage')} className={styles.iconwrapper}  ><FontAwesomeIcon className={styles.iconssize} icon={faHeart} /><div></div>מודעות שמורות</div>
                <div onClick={() => navigate('../lastSearches')} className={styles.iconwrapper}  ><FontAwesomeIcon className={styles.iconssize} icon={faMagnifyingGlass} /><div>חיפושים אחרונים</div></div>
            </div>


            <div className={styles.twobuttons} >
                <div className={styles.bluebutton} ><FontAwesomeIcon className={styles.iconsize} icon={faPerson} /></div>
                <div className={styles.whitebutton} ><FontAwesomeIcon className={styles.iconsize} icon={faPhone} /></div>
            </div>

            <div className={styles.disconnect} >התנתקות</div>
        </div>
    )
}

export default SideBar