import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Link, useNavigate } from 'react-router-dom'
import { useForm } from "react-hook-form";
import React, { useState } from 'react'
import styles from './PersonalZone.module.scss'
import axios from 'axios';
import { useGlobalContext } from './redux/actions'
import { faEnvelope, faPencil, faMagnifyingGlass, faHeart, faPerson, faPhone, faChevronDown } from '@fortawesome/free-solid-svg-icons'
import PersonalMessages from './components/PersonalZone/PersonalMessages';
import EditPersonalInfo from './components/PersonalZone/EditPersonalInfo';
import SideBar from './components/PersonalZone/SideBar';



function PersonalZone() {
    const { loggedName, loggedfamilyname, loggedmail, loggedphone } = useGlobalContext()
    const [messages, setmessages] = useState(true)
    const [editinfo, setEditinfo] = useState(false)
    const { register, setValue, handleSubmit, watch, formState: { errors } } = useForm();
    const values = watch()
    const navigate = useNavigate()

    function onSubmit() {
        axios.post(`http://localhost:8080/updatePersonalInformation`, { values, loggedName }).then((res) => {
            console.log(res.data)
        })
    }


    return (
        <div className={styles.flexcol} >


            <SideBar messages={messages} setmessages={setmessages} editinfo={editinfo} setEditinfo={setEditinfo} />

            <div className={styles.right} >


                {messages &&
                    <PersonalMessages />

                }


                {editinfo &&
                    <EditPersonalInfo loggedName={loggedName} register={register} setValue={setValue} loggedfamilyname={loggedfamilyname}
                        loggedphone={loggedphone} loggedmail={loggedmail} handleSubmit={handleSubmit} onSubmit={onSubmit} />
                }

            </div>

        </div>
    )
}

export default PersonalZone