import React, { useState, useEffect } from 'react';
import styles from './ResultExpand.module.scss'
import { useForm } from "react-hook-form";
import { Link, useNavigate } from 'react-router-dom'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSnowflake } from '@fortawesome/free-solid-svg-icons'
import { faHouseUser } from '@fortawesome/free-solid-svg-icons'
import { faWarehouse } from '@fortawesome/free-solid-svg-icons'
import { faCouch } from '@fortawesome/free-solid-svg-icons'
import { faWheelchair } from '@fortawesome/free-solid-svg-icons'
import { faElevator } from '@fortawesome/free-solid-svg-icons'
import { faHammer } from '@fortawesome/free-solid-svg-icons'
import { faFaucet } from '@fortawesome/free-solid-svg-icons'
import { faWindowClose } from '@fortawesome/free-solid-svg-icons'
import { faSun } from '@fortawesome/free-solid-svg-icons'
import { faPhone } from '@fortawesome/free-solid-svg-icons';
import { faXmark } from '@fortawesome/free-solid-svg-icons';
import PH from './PH.png'
import ad1 from '../pics/ad1.gif'
import ad2 from '../pics/ad2.jpg'
import ad3 from '../pics/ad3.jpg'
function ResultExpand({ id_, setid_, expandedResult, setexpandedResult, condense, setisOpen }) {
    const [showPhone, setshowPhone] = useState(false)
    const [mail, setmail] = useState(false)
    const [modal, setmodal] = useState(false)
    const [mailform, setmailform] = useState(false)
    const { register, reset, handleSubmit, watch, formState: { errors } } = useForm();
    const [activatebutton, setactivatebutton] = useState(false)
    const values = watch()


    function validateInput() {
        if (values.clientName.length != undefined && values.clientPhone.length != undefined &&
            values.clientMail.length != undefined && values.clientDescription.length != undefined) {
            setactivatebutton(true)
        }

    }
    return (
        <div>


            <div id={id_} onClick={() => condense(id_, setid_, setshowPhone, setisOpen)} className='none' >
                <div id='mainAlt' className={styles.mainalt}>               {/* main div */}
                    {/* colored div */}
                    <div id='orange' className={
                        expandedResult.paymentPath == 3 ? 'altorange' :
                            expandedResult.paymentPath == 2 ? 'altrose' :
                                'altregular'}  >

                        <div onClick={() => { document.getElementById('navbar').style.display = 'none' }} className={styles.altimgwrapper}>       {/* sub - orange div - right (img)*/}

                            <Link to={'/gallery'} state={expandedResult}>  <img className={styles.altimg} src={expandedResult.main}></img></Link>
                        </div>



                        <div className={styles.orangevertical}>


                            <div>
                                <div className={styles.altadress}>        {/* sub - orange div - right (adress)*/}
                                    <div>{expandedResult.street}</div>
                                    <div>{expandedResult.houseNumber}</div>
                                </div>




                                <div className={styles.altkind}>
                                    <div>{expandedResult.kind}</div>
                                    <div>{`${expandedResult.settlement}, `}</div>
                                </div>
                            </div>



                            <div className={styles.altM2}>    {/* sub - orange div - right (rooms, floor, m^2)*/}

                                <div className={styles.altM2inner}>
                                    <div>חדרים</div>
                                    <div className={styles.innernumberssmall} >{expandedResult.numberOfRooms}</div>
                                </div>

                                <div className={styles.altM2inner} >
                                    <div>קומה</div>
                                    <div className={styles.innernumberssmall} >{expandedResult.floorNumber}</div>
                                </div>

                                <div className={styles.altM2inner} >
                                    <div>מ"ר</div>
                                    <div className={styles.innernumberssmall} >{expandedResult.quadrateMeterTotal}</div>
                                </div>

                            </div>


                        </div>


                        <div className={styles.lastorangepart}>

                            <div>
                                <div>{expandedResult.price}</div>
                                <div className={styles.updatetitle}>עודכן היום</div>
                                <div>   </div>
                            </div>

                            <div className={styles.flexphone}   >
                                <FontAwesomeIcon className={styles.phoneicon} icon={faPhone}></FontAwesomeIcon>
                                <button onClick={(e) => {
                                    e.stopPropagation()
                                    setshowPhone(!showPhone)
                                }} className={styles.greenbutton}>הצגת מספר טלפון</button>
                                {showPhone &&
                                    <div className={styles.phonedetails}>

                                        <div className={styles.phonedetailspadding}>{expandedResult.contactName}    </div> {/* or contactName */}
                                        <div className={styles.bar}>  </div>
                                        <div className={styles.phonedetailspadding} > {expandedResult.phone}  </div>
                                        <div className={styles.bar} > </div>
                                        <div
                                            onMouseEnter={() => setmail(!mail)}
                                            onMouseLeave={() => setmail(!mail)}
                                            onClick={() => setmodal(true)}
                                            className={!mail ? styles.regularcolor : styles.orangecolor}>  שליחת דוא"ל למפרסם </div>
                                    </div>
                                }
                            </div>


                        </div>

                    </div>


                    <div className={styles.wrapperwhite}>

                        <div className={styles.mainwhite}>    {/* the large white bar with props. and desc. */}
                            <div className={styles.title}>תיאור הנכס</div>
                            <div className={styles.text}>{expandedResult.description}</div>

                            <div className={styles.methaprops}>                           {/* props */}

                                <div className={styles.mainpropscol}>                        {/* props 1 */}
                                    <div className={styles.propscol}>
                                        <div className={styles.propstext}>מצב הנכס</div>
                                        <div className={styles.propstextb} >{expandedResult.condition}</div>
                                    </div>

                                    <div className={styles.propscol}>
                                        <div className={styles.propstext} >מ"ר גינה</div>
                                        <div className={styles.propstextb} >{expandedResult.quadrateMeterGarden}</div>
                                    </div>

                                    <div className={styles.propscol}>
                                        <div className={styles.propstext} >מרפסת</div>
                                        <div className={styles.propstextb} > {expandedResult.numberOfBalconies}</div>
                                    </div>

                                </div>

                                <div>                            {/* props2 */}

                                    <div className={styles.propscol}>
                                        <div className={styles.propstext}>  תאריך כניסה</div>
                                        <div className={styles.propstextb} >{expandedResult.enteringDate}</div>
                                    </div>

                                    <div className={styles.propscol}>
                                        <div className={styles.propstext} >מ"ר בנוי</div>
                                        <div className={styles.propstextb} >{expandedResult.quadrateMeter}</div>
                                    </div>

                                    <div className={styles.propscol}>
                                        <div className={styles.propstext} >חניות</div>
                                        <div className={styles.propstextb} > {expandedResult.numberOfParkings}</div>
                                    </div>



                                </div>

                            </div>

                            <div className={styles.mainicon}>               {/* icons */}

                                <div className={styles.iconscol}>
                                    <div className={(expandedResult.mazgan === 1) ? styles.activecolor : styles.deactivatedcolor}>   <FontAwesomeIcon icon={faSnowflake} /><div className={styles.icontext}> מיזוג  </div></div>
                                    <div className={(expandedResult.mamad === 1) ? styles.activecolor : styles.deactivatedcolor}>    <FontAwesomeIcon icon={faHouseUser} /><div className={styles.icontext}> מחסן  </div></div>
                                    <div className={(expandedResult.shelter === 1) ? styles.activecolor : styles.deactivatedcolor}>  <FontAwesomeIcon icon={faWarehouse} /><div className={styles.icontext}> ממ"ד  </div></div>
                                </div>

                                <div className={styles.iconscol} >
                                    <div className={(expandedResult.furniture === 1) ? styles.activecolor : styles.deactivatedcolor}>  <FontAwesomeIcon icon={faCouch} /><div className={styles.icontext}> ריהוט  </div></div>
                                    <div className={(expandedResult.disabled === 1) ? styles.activecolor : styles.deactivatedcolor}>     <FontAwesomeIcon icon={faWheelchair} /><div className={styles.icontext}> גישה לנכים   </div></div>
                                    <div className={(expandedResult.elevator === 1) ? styles.activecolor : styles.deactivatedcolor}>  <FontAwesomeIcon icon={faElevator} /><div className={styles.icontext}>   מעלית  </div></div>
                                </div>

                                <div className={styles.iconscol} >
                                    <div className={(expandedResult.tadiran === 1) ? styles.activecolor : styles.deactivatedcolor}> <FontAwesomeIcon icon={faSnowflake} /><div className={styles.icontext}> מזגן תדיראן  </div></div>
                                    <div className={(expandedResult.repaired === 1) ? styles.activecolor : styles.deactivatedcolor}>    <FontAwesomeIcon icon={faHammer} /><div className={styles.icontext}> משופצת  </div></div>
                                    <div className={(expandedResult.citchen === 1) ? styles.activecolor : styles.deactivatedcolor}> <FontAwesomeIcon icon={faFaucet} /><div className={styles.icontext}> מטבח  </div></div>
                                </div>

                                <div className={styles.iconscol} >
                                    <div className={(expandedResult.boiler === 1) ? styles.activecolor : styles.deactivatedcolor}>    <FontAwesomeIcon icon={faSun} /><div className={styles.icontext}> דוד שמש  </div></div>
                                    <div className={(expandedResult.bars === 1) ? styles.activecolor : styles.deactivatedcolor}>    <FontAwesomeIcon icon={faWindowClose} /><div className={styles.icontext}> סורגים  </div></div>
                                    <div className={(expandedResult.mazgan === 1) ? styles.activecolor : styles.deactivatedcolor}>  <FontAwesomeIcon icon={faHouseUser} /><div className={styles.icontext}> יחידת דיור  </div></div>

                                </div>




                            </div>
                            <img className={styles.phimg} src={PH} ></img>

                        </div>



                        <div className={styles.sideadd}>

                            <img className={styles.firstadd} src={ad1}></img>
                            <img className={styles.secondadd} src={ad2}></img>
                            <img className={styles.secondadd} src={ad3}></img>


                        </div>



                    </div>



                </div>

            </div>




            {modal &&
                <div className={styles.modal} onClick={() => setmodal(false)} >
                    <div className={styles.innermodal}>
                        <div className={styles.mainsendtitle}  >כדאי לשים לב</div>
                        <div className={styles.sendtext}  >אנו ממליצים להמנע מהעברת כספים טרם קבלת המוצר ולהיזהר מעסקאות בהן המוצר צפוי להישלח אליכם לאחר תשלום</div>
                        <div className={styles.send}>
                            <div>חזרה</div>
                            <div onClick={(e) => {
                                e.stopPropagation()
                                setmodal(false)
                                setmailform(true)
                            }}>שליחת הודעה</div>
                        </div>
                    </div>
                </div>

            }
            {mailform &&
                <div className={styles.modal}>
                    <div className={styles.innermodal2}>

                        <div className={styles.mailflex}  >
                            <div>שליחת דוא"ל למפרסם</div>
                            <FontAwesomeIcon className={styles.cursor} icon={faXmark} onClick={() => {
                                setmodal(false)
                                setmailform(false)
                                reset()
                            }} ></FontAwesomeIcon>
                        </div>

                        <form onChange={() => validateInput()} >
                            <div>
                                <input {...register("clientName")} className={styles.mailinput} placeholder='השם שלך'></input>
                                <input {...register("clientPhone")} className={styles.mailinput} placeholder='מספר הטלפון שלך' ></input>
                                <input {...register("clientMail")} className={styles.mailinput} placeholder='כתובת דוא"ל לקבלת תושבה'></input>
                            </div>
                        </form>

                        <textarea {...register("clientDescription")} className={styles.textarea} placeholder='תיאור' ></textarea>

                        {!activatebutton && <div>
                            <button>ביטול</button>
                            <button>שליחה</button>

                        </div>}
                        {activatebutton && <div className={styles.buttonswrapper}>
                            <div className={styles.cancel} onClick={() => {
                                setmodal(false)
                                setmailform(false)
                                reset()
                            }}  >ביטול</div>
                            <button className={styles.sendmailorangebutton} >שליחה</button>

                        </div>}
                    </div>
                </div>

            }




        </div>
    )
}

export default ResultExpand