import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronDown } from '@fortawesome/free-solid-svg-icons'
import styles from './PersonalMessages.module.scss'
import piano from '../pics/piano.svg'

function PersonalMessages() {
    return (
        <div id='messages' >

            <div className={styles.title} >המודעות שלי</div>


            <div className={styles.flexbox} >

                <div className={styles.titles}  >סינון לפי:</div>
                <div className={styles.whitebox} >
                    <div className={styles.chevwrapper}  >   <div>קטגוריה</div> <FontAwesomeIcon icon={faChevronDown} /></div>
                </div>
                <div className={styles.whitebox} >
                    <div className={styles.chevwrapper}   >   <div>סטטוס</div> <FontAwesomeIcon icon={faChevronDown} /></div>
                </div>
            </div>
            <div className={styles.flexpiano} >

                <div className={styles.pianotext} >

                    <div className={styles.titlelarge} >אין כרגע מודעות פעילות</div>

                    <div className={styles.regtext}  >רוצה להעביר את הפסנתר הישן שלך הלאה
                        למכור את הרכב או להשכיר את הדירה?</div>

                    <div className={styles.whitebutton2} >לחצו פה לפרסום מודעה</div>



                </div>


                <div className={styles.pianoimg}>

                    <img src={piano} ></img>
                </div>



            </div>

        </div>
    )
}

export default PersonalMessages