import axios from 'axios'
import AdvancedSearch from '../Search/AdvancedSearch'


// input chevrone rotate
export function rotate(rotateCount, setrotateCount, e, deg1, deg2) {
    setrotateCount(rotateCount + 1)
    let id = e.currentTarget.id

    if (rotateCount % 2 == 0) {
        document.getElementById(`${id}`).style.transform = `rotate(${deg1}deg)`
    }

    else {
        document.getElementById(`${id}`).style.transform = `rotate(${deg2}deg)`

    }


}

// find settlement by ןnput
export function findSettlement(data, setStreet, setCity) {
    setStreet([])
    setCity([])
    if (data.length > 1 && (data[0] != ' ' && data[1] != ' ')) {
        axios.get(`https://data.gov.il/api/3/action/datastore_search?resource_id=9ad3862c-8391-4b2f-84a4-2d4c68625f4b&q=${data}`).then((res) => {

            let temporaryData = res.data.result.records.slice(0, 7)
            let temp = []

            for (let i = 0; i < temporaryData.length; i++) {

                if (!(temp.includes(temporaryData[i].שם_ישוב)) && temporaryData[i].שם_ישוב.includes(data)) {
                    setCity(c => [...c, temporaryData[i].שם_ישוב])
                    temp[i] = temporaryData[i].שם_ישוב
                }

                setStreet(s => [...s, `${temporaryData[i].שם_רחוב}, ${temporaryData[i].שם_ישוב}`])
            }
        })
    }

}

//expand add
export function exp(r, index, setid_, setexpandedResult, isOpen, setisOpen) {


    // level 1
    if (!isOpen) {
        document.getElementById(r.M_id).style.height = '600px'
        document.getElementById(r.M_id).style.display = 'flex'

        document.getElementById(r.M_id + 1).style.display = 'none'
        document.getElementById(r.M_id + 2).style.display = 'none'
        document.getElementById(r.M_id + 5).style.display = 'none'

        if (index == 0) {
            document.getElementById('mainAlt').style.top = '-42px'
            document.getElementById('mainAlt').style.borderTop = 'none'

        }
        if (index == 1) {
            document.getElementById('mainAlt').style.top = '58px'
            document.getElementById('mainAlt').style.borderTop = '1px solid #ccc'


        }
        else if (index !== 0 && index !== 1) {
            let height = (index - 1) * 100
            height += 70
            document.getElementById('mainAlt').style.top = `${height}px`
            document.getElementById('mainAlt').style.borderTop = 'none'

        }

        setid_(r.M_id + 1000)
        setexpandedResult(r)
        setisOpen(true)
    }


}



//condence add
export function condense(id, setid_, setshowPhone, setisOpen) {
    setshowPhone(false)
    document.getElementById(id - 1000).style.height = '100px'

    document.getElementById((id - 1000) + 1).style.display = 'flex'

    document.getElementById((id - 1000) + 2).style.display = 'flex'

    document.getElementById((id - 1000) + 5).style.display = 'flex'
    document.getElementById(id - 1000 + 5).style.justifyContent = 'flex-end'


    document.getElementById(id).style.display = 'none'

    setid_(id - 1000)
    setisOpen(false)
    // setexpandedResult(r) 
}
let counter = 2
//change heart-on-add-picture color from white to orange
export function changeHeartColor(index) {

    if (counter % 2 == 0) {
        document.getElementById(index).style.display = 'none'
        document.getElementById(index + 100).style.display = 'flex'
    }

    else if (counter % 2 != 0) {
        document.getElementById(index).style.display = 'flex'
        document.getElementById(index + 100).style.display = 'none'
    }

    counter += 1


}
//saving props of last search to DB
export async function saveLastSearch(basicData, loggedName) {
    let lastSearhInfo = {
        city: false,
        kind: false,
        roomsFrom: false,
        roomsTo: false,
        pricefrom: false,
        priceto: false,
        desription: false,
        sizeFrom: false,
        sizeTo: false,
        floorFrom: false,
        floorTo: false,
        balcony: false,
        bars: false,
        diabled: false,
        elevator: false,
        loggedName: loggedName


    }

    const time = new Date()
    lastSearhInfo.postTimeLocal = (`${time.getHours()}:${time.getMinutes()}`)
    lastSearhInfo.postTimeMonth = (`${time.getMonth()}`)
    lastSearhInfo.postTimeDate = (`${time.getDate()}`)

    if (basicData[1] != undefined && basicData[1] != '') lastSearhInfo.date = basicData[1]   // date
    if (basicData[3] != undefined && basicData[3] != '') lastSearhInfo.city = basicData[3]   // city
    if (basicData[5].REprops != undefined && basicData[5].REprops != '' && basicData[5].REprops != []) lastSearhInfo.kind = basicData[5].REprops // kind
    if (basicData[5].roomsFrom != undefined && basicData[5].roomsFrom != '') lastSearhInfo.roomsFrom = basicData[5].roomsFrom //  rooms
    if (basicData[5].roomsTo != undefined && basicData[5].roomsTo != '') lastSearhInfo.roomsTo = basicData[5].roomsTo //  rooms
    if (basicData[6] != undefined && basicData[6] != '') lastSearhInfo.pricefrom = basicData[6]   // price
    if (basicData[7] != undefined && basicData[7] != '') lastSearhInfo.priceto = basicData[7]   // price

    if (basicData[0].desription != undefined && basicData[0].desription != '') lastSearhInfo.desription = basicData[0].desription

    if (basicData[0].sizeFrom != undefined && basicData[0].sizeFromn != '') lastSearhInfo.sizeFrom = basicData[0].sizeFrom
    if (basicData[0].sizeTo != undefined && basicData[0].sizeTo != '') lastSearhInfo.sizeTo = basicData[0].sizeTo

    if (basicData[0].floorFrom != undefined && basicData[0].floorFrom != '') lastSearhInfo.floorFrom = basicData[0].floorFrom
    if (basicData[0].floorTo != undefined && basicData[0].floorTo != '') lastSearhInfo.floorTo = basicData[0].floorTo

    if (basicData[0].balcony === true) lastSearhInfo.balcony = basicData[0].balcony
    if (basicData[0].bars === true) lastSearhInfo.bars = basicData[0].bars
    if (basicData[0].disabled === true) lastSearhInfo.diabled = basicData[0].diabled
    if (basicData[0].elevator === true) lastSearhInfo.elevator = basicData[0].elevator


    let res_ = await axios.post(`http://localhost:8080/lastSearch`, lastSearhInfo)


}

//loading primary data on onLoad
export async function initialNoSearchResults(setresults, initialLimit) {
    let arr = []

    let sql = `SELECT * FROM NonMandatoryObject 
        JOIN Object ON Object.M_id = NonMandatoryObject.NM_id
        JOIN Pictures ON Pictures.PICS_id = NonMandatoryObject.NM_id  `

    let res = await axios.get(`http://localhost:8080/search`, { params: [arr, sql, 'placeholder', initialLimit] })

    setresults(res.data)

}

// choosing the desired settlement from the proposed list
export function handleAdress(data, setCity, setStreet, setadress, setchosenCity, setchosenStreet) {
    setCity([])
    setStreet([])
    setadress(data.target.innerText)
    if (data.target.id === 'city') {
        setchosenCity(data.target.innerText.split('\n')[0])

        setchosenStreet('')
    }

    if (data.target.id === 'street') {
        setchosenStreet(data.target.innerText.split(",")[0].trim())
        setchosenCity('')
    }

}
