import React, { useState } from 'react'
import styles from './Kind.module.scss'
import { rotate } from '../../functions/utils.js'
import { useGlobalContext } from '../../../redux/actions'

function Kind({ setActivatekind, setestateKind, estateKind, setestateArray }) {
    const [Kind1, setKind1] = useState(false)
    const [Kind2, setKind2] = useState(false)
    const [Kind3, setKind3] = useState(false)
    const [REproperties, setREproperties] = useState([])
    const { REprops } = useGlobalContext()
    const [rotateCount, setrotateCount] = useState(1)
    const [estateCounter, setestateCounter] = useState(2)

    function ischeced(e) { // choosing item in category (checkbox)
        if (e.target.checked) {
            setREproperties(p => [...p, e.target.id])

            if (estateKind.length === 0) setestateKind(e.target.id)
            else if (estateKind.length > 0) {
                setestateCounter(estateCounter + 1)
                setestateKind(`${estateCounter} סוגי נכסים`)
            }
        }
        else if (!(e.target.checked)) {
            if (estateKind.length === 0) setestateKind([])
            else if (estateKind.length > 0) {
                setestateCounter(estateCounter - 1)
                setestateKind(`${estateCounter} סוגי נכסים`)
            }
        }
    }
    function estateCategory(e) {// choosing   category (checkbox)

        if (e.target.id === 'flats') {
            if (e.target.checked) setestateKind(`${9} סוגי נכסים`)
            else setestateKind([])


        }
        if (e.target.id === 'houses') {
            if (e.target.checked) setestateKind(`${4} סוגי נכסים`)
            else setestateKind([])

        }
        if (e.target.id === 'additional') {
            if (e.target.checked) setestateKind(`${7} סוגי נכסים`)
            else setestateKind([])
        }

        if (e.target.id === 'all' && e.target.checked) {
            setestateKind([])
            document.getElementById('flats').checked = true;
            document.getElementById('houses').checked = true;
            document.getElementById('additional').checked = true;
        }

        if (e.target.id === 'all' && !(e.target.checked)) {
            setestateKind([])
            document.getElementById('flats').checked = false;
            document.getElementById('houses').checked = false;
            document.getElementById('additional').checked = false;
        }

    }


    function onSubmit() {
        REprops(REproperties)
    }


    return (
        <div className={styles.wrapper}>
            <div className={styles.inside} >

                <div className={styles.bars}>

                    <div className={styles.flex}>
                        <input id='all' onClick={(e) => estateCategory(e)} className={styles.boxwidth} type='checkbox'></input>
                        <div className={styles.allestates}>כל סוגי הנכסים</div>
                    </div>
                </div>

                <div className={styles.bars} >
                    <div className={styles.flex}>
                        <input id='flats' onClick={(e) => estateCategory(e)} className={styles.boxwidth} type='checkbox'></input>
                        <div className={styles.allestates}>   דירות</div>

                        <select id='selectSection1' onClick={(e) => {
                            rotate(rotateCount, setrotateCount, e, 360, 180)
                            setKind1(!Kind1)
                        }} className={styles.select} ></select>

                    </div>
                </div>

                {Kind1 &&
                    <>

                        <div className={styles.flex2}>

                            <input id='דירה' onChange={(e) => ischeced(e)} className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>   דירה</div>
                        </div>
                        <div className={styles.line}></div>


                        <div className={styles.flex2}>
                            <input id='דירת גג' onChange={(e) => ischeced(e)} className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>   דירת גג</div>
                        </div>
                        <div className={styles.line}></div>



                        <div className={styles.flex2}>
                            <input id='גג/פנטהאוז' onChange={(e) => ischeced(e)} className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>   גג/פנטהאוז</div>
                        </div>
                        <div className={styles.line}></div>


                        <div className={styles.flex2}>
                            <input onChange={(e) => ischeced(e)} id='דופלקס' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>   דופלקס</div>
                        </div>
                        <div className={styles.line}></div>


                        <div className={styles.flex2}>
                            <input onChange={(e) => ischeced(e)} id='דירת נופש' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>   דירת נופש</div>
                        </div>
                        <div className={styles.line}></div>


                        <div className={styles.flex2}>
                            <input onChange={(e) => ischeced(e)} id='מרתף/פרטר' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>   מרתף/פרטר</div>
                        </div>
                        <div className={styles.line}></div>


                        <div className={styles.flex2}>
                            <input onChange={(e) => ischeced(e)} id='טריפלקס' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>   טריפלקס</div>
                        </div>
                        <div className={styles.line}></div>


                        <div className={styles.flex2}>
                            <input onChange={(e) => ischeced(e)} id='יחידת דיור' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>   יחידת דיור</div>
                        </div>
                        <div className={styles.line}></div>


                        <div className={styles.flex2}>
                            <input onChange={(e) => ischeced(e)} id='סטודיו' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>   סטודיו</div>
                        </div>
                        <div className={styles.line}></div>

                    </>
                }


                <div className={styles.bars} >
                    <div className={styles.flex}>
                        <input id='houses' onClick={(e) => estateCategory(e)} className={styles.boxwidth} type='checkbox'></input>
                        <div className={styles.allestates}>   בתים</div>

                        <select id='selectSection2' onClick={(e) => {
                            rotate(rotateCount, setrotateCount, e, 360, 180)
                            setKind2(!Kind2)
                        }} className={styles.select} ></select>


                    </div>
                </div>
                {Kind2 &&
                    <>

                        <div className={styles.flex2}>

                            <input onChange={(e) => ischeced(e)} id='בית פרטי' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>   בית פרטי</div>
                        </div>
                        <div className={styles.line}></div>


                        <div className={styles.flex2}>
                            <input onChange={(e) => ischeced(e)} id='דו משפחתי' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>     דו משפחתי</div>
                        </div>
                        <div className={styles.line}></div>



                        <div className={styles.flex2}>
                            <input onChange={(e) => ischeced(e)} id='משק חקלאי ' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>    משק חקלאי   </div>
                        </div>
                        <div className={styles.line}></div>


                        <div className={styles.flex2}>
                            <input onChange={(e) => ischeced(e)} id='משק עזר' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>   משק עזר</div>
                        </div>
                        <div className={styles.line}></div>



                    </>
                }



                <div className={styles.bars} >
                    <div className={styles.flex}>
                        <input id='additional' onClick={(e) => estateCategory(e)} className={styles.boxwidth} type='checkbox'></input>
                        <div className={styles.allestates}>   סוגים נוספים</div>
                        <select id='selectSection3' onClick={(e) => {
                            rotate(rotateCount, setrotateCount, e, 360, 180)
                            setKind3(!Kind3)
                        }} className={styles.select} ></select>
                    </div>
                </div>

                {Kind3 &&
                    <>

                        <div className={styles.flex2}>

                            <input onChange={(e) => ischeced(e)} id='מגרשים' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>     מגרשים</div>
                        </div>
                        <div className={styles.line}></div>


                        <div className={styles.flex2}>
                            <input onChange={(e) => ischeced(e)} id='דיור מוגן' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>       דיור מוגן</div>
                        </div>
                        <div className={styles.line}></div>



                        <div className={styles.flex2}>
                            <input onChange={(e) => ischeced(e)} id='בניין מגורים' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>      בניין מגורים   </div>
                        </div>
                        <div className={styles.line}></div>


                        <div className={styles.flex2}>
                            <input onChange={(e) => ischeced(e)} id='מחסן' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>     מחסן</div>
                        </div>
                        <div className={styles.line}></div>


                        <div className={styles.flex2}>
                            <input onChange={(e) => ischeced(e)} id='חניה' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>     חניה</div>
                        </div>
                        <div className={styles.line}></div>

                        <div className={styles.flex2}>
                            <input onChange={(e) => ischeced(e)} id="קב' רכישה" className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>     קב' רכישה</div>
                        </div>
                        <div className={styles.line}></div>

                        <div className={styles.flex2}>
                            <input onChange={(e) => ischeced(e)} id='כללי' className={styles.boxwidth} type='checkbox'></input>
                            <div className={styles.allestates}>     כללי </div>
                        </div>
                        <div className={styles.line}></div>


                    </>
                }


                <div className={styles.choise} onClick={() => {
                    onSubmit()
                    setActivatekind(false)
                }} >בחירה</div>

            </div>



        </div>
    )
}

export default Kind