import React from 'react'
import styles from './ImageOne.module.scss'
import download_qr from '../pics/download_qr.png'
import android from '../pics/download_android.png'
import apple from '../pics/download_iphone.png'
import jinjit from '../pics/jinjit2.png'
import yadBG from '../pics/yadBG.png'

function Img1() {
    return (
        <div>
            <img className={styles.imgBG} src={yadBG}></img>


            <div className={styles.row}>
                <div className={styles.orangetitle}>האפליקציה החדשה  </div>
                <div className={styles.title}>שלנו</div>
            </div>
            <div className={styles.title2}>לא מפספסת כלום</div>



            <div className={styles.images}>


                <div className={styles.innerimages}>
                    <img className={styles.appleAndroidImg} src={android}></img>
                    <img className={styles.appleAndroidImg} src={apple}></img>

                </div>




                <div>
                    <img className={styles.box} src={download_qr}></img>

                </div>


                <div className={styles.wrapper}> <img className={styles.jinjit} src={jinjit}></img></div>

            </div>




        </div>
    )
}

export default Img1