import React from 'react'
import { useLocation } from 'react-router-dom'
import styles from './ExpandedPicture.module.scss'
import x from './X.png'
import { Link, useNavigate } from 'react-router-dom'

function ExpandedPicture() {
    const location = useLocation()

    return (
        <div>
            <div className={styles.btn}>
                <Link to='../SearchRE' className={styles.text}>סגירת גלריה</Link>
                <img className={styles.img} src={x}></img>
            </div>


            <img className={styles.img2} src={location.state.main}></img>


        </div>
    )
}

export default ExpandedPicture


