import React, { useState, useEffect } from 'react'
import { useGlobalContext } from '../../redux/actions'
import styles from './FavPage.module.scss'
import girl from '../../components/pics/girl.png'
import orangeHeart from '../../components/pics/orangeHeart.png'
import { useLocation } from 'react-router-dom'
import FavPageExtension from './FavPageExtension'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faRotateLeft, faShekelSign, faXmark } from '@fortawesome/free-solid-svg-icons'
import { faPrint } from '@fortawesome/free-solid-svg-icons'
import { faPerson } from '@fortawesome/free-solid-svg-icons'
import { faShareNodes } from '@fortawesome/free-solid-svg-icons'
import { faPlus } from '@fortawesome/free-solid-svg-icons'
import { expandTextArea, callResultMemo, computeLength, computeLength2, condenseTextArea, changeTextToOrange, cancelChanges, updateAdd, restore } from './navbarUtils'
function FavPage() {
    const { favAdd, updatefavAdd, restorefavAdd } = useGlobalContext()
    const [greyfav, setgreyfav] = useState(false)
    const location = useLocation()
    const [modaladdInfo, setmodaladdInfo] = useState(false)
    const [currentImg, setcurrentImg] = useState('')
    const [imgcounter, setimgcounter] = useState(0) // picture slide-show counter
    const [totalimgcounter, settotalimgcounter] = useState(0) // picture slide-show counter
    const [showPhone, setshowPhone] = useState(false)
    const [mailmodal, setmailmodal] = useState(false)
    const [advancedMailModal, setadvancedMailModal] = useState(false)
    const [activatebutton, setactivatebutton] = useState(false)
    const [extension, setextension] = useState(false)
    const [textareatext, settextareatext] = useState('')
    const [lettersNumber, setlettersNumber] = useState(0)
    const [filtredArray, setfiltredArray] = useState([])
    const [removedResult, setremovedResult] = useState(null)


    return (

        <div className={styles.mainBG} >
            {favAdd.length > 0 && <div className={styles.width}  >

                <div className={styles.topwrapper}>
                    <div className={styles.toptitle}  >מודעות שאהבתי</div>
                    <div className={styles.accessability}> <FontAwesomeIcon className={styles.personicon} icon={faPerson} /> <div>נגישות</div></div>
                </div>

                <div className={styles.buttonswrapper_} >
                    <div className={styles.REwrapper} >
                        <div className={styles.titleRE} >מודעות נדל"ן</div>
                        <div className={styles.print} ><FontAwesomeIcon icon={faPrint} /> <div>הדפסת הרשימה</div></div>
                    </div>
                    <div className={styles.share} >
                        <FontAwesomeIcon icon={faShareNodes} /> <div>שיטוף</div>
                    </div>
                </div>


                {favAdd.map((r, index) =>

                    <div
                        onMouseEnter={() => {
                            document.getElementById(r.M_id).style.boxShadow = '2px 2px 4px 2px rgb(12 0 51 / 10%)'
                            changeTextToOrange(r.M_id)
                        }}

                        onMouseLeave={() => {
                            document.getElementById(r.M_id).style.boxShadow = 'none'
                            cancelChanges(r.M_id)
                        }}
                    >
                        <div id={r.M_id}
                            className={styles.mainwrapper_} onClick={() => {
                                setcurrentImg(r.main)
                                setextension(true)
                            }}  >




                            <div id={r.M_id + 1} className={styles.frame1}  >  {/* first section (right one) */}

                                <div className={styles.frame3}>

                                    {r.main &&
                                        <div  >
                                            <div className={styles.heartwrapper} onClick={(e) => {
                                                e.stopPropagation()
                                                updateAdd(r.M_id, favAdd, updatefavAdd, 'delete', setfiltredArray, filtredArray, setremovedResult, removedResult)

                                            }}  >
                                                <img id={index} className={styles.hollowheart} src={orangeHeart}></img>
                                                <img id={index + 100} className={styles.hollowheartAlt} src={orangeHeart}></img>

                                            </div>
                                            <img className={styles.img} src={r.main} ></img>
                                        </div>}


                                    {!r.main && <div className={styles.noimg2}  >ללא תמונה</div>}

                                </div>

                                <div className={styles.frame4} >

                                    <div className={styles.inner1}>
                                        <div className={styles.streetmargin}>{r.street}</div>
                                        <div>{r.houseNumber}</div>
                                    </div>

                                    <div className={styles.inner2} >
                                        <div className={styles.streetmargin} >{`${r.settlement}, `}</div>
                                        <div>{r.kind}</div>
                                    </div>




                                </div>


                            </div>

                            <div id={r.M_id + 2} className={styles.frame2}  >

                                <div className={styles.column}>
                                    <div className={styles.numbers} >{r.numberOfRooms}</div>
                                    <div className={styles.numberstitles}  >חדרים</div>
                                </div>

                                <div className={styles.column}>
                                    <div className={styles.numbers} >{r.floorNumber}</div>
                                    <div className={styles.numberstitles}  >קומה</div>
                                </div>

                                <div className={styles.column}>
                                    <div className={styles.numbers} >{r.quadrateMeterTotal}</div>
                                    <div className={styles.numberstitles}  >מ"ר</div>
                                </div>
                            </div>



                            <div id={r.M_id + 5} className='frame5' >
                                <div className={styles.flexcol3} >
                                    <div className='white' id={`uppertext${r.M_id}`} >פתיחה בטאב חדש</div>
                                    <div className={styles.niswrapper} >
                                        {(r.price > 0) ? r.price : 'אין מחיר'}
                                        {(r.price > 0) && <FontAwesomeIcon className={styles.nissign} icon={faShekelSign} ></FontAwesomeIcon>}
                                    </div>
                                    <div id={`lowertext${r.M_id}`} className={styles.updatetext}  >עודכן היום</div>
                                </div>


                            </div>



                            {extension && <FavPageExtension f={r} setcurrentImg={setcurrentImg} setimgcounter={setimgcounter} settotalimgcounter={settotalimgcounter}
                                setmodaladdInfo={setmodaladdInfo} currentImg={currentImg} totalimgcounter={totalimgcounter} imgcounter={imgcounter}
                                setmailmodal={setmailmodal} index={index} setextension={setextension}   ></FavPageExtension>}

                        </div>

                        <div id={`comment${r.M_id}`} className={styles.commentwrapper} onClick={(e) => {

                            expandTextArea(e, r.M_id)
                        }} > {/* non expanded text area */}
                            <div id={`textareafirst${r.M_id}`} className={styles.textarea} >
                                <div className={styles.comment} >


                                    <FontAwesomeIcon icon={faPlus} />
                                    <div id={`eliminate${r.M_id}`} >הוספת הערה</div>

                                    <div id={`newtext${r.M_id}`}></div> </div>
                            </div>
                        </div>

                        <div id={`deleted${r.M_id}`} className={styles.deleted} >
                            <div className={styles.metharestorewrapper} >
                                <div> {`המודעה "${r.street}..." הוסרה  `}</div>
                                <div className={styles.metharestore} >
                                    <div className={styles.restore} onClick={(e) => {
                                        e.stopPropagation()
                                        updateAdd(r.M_id, favAdd, updatefavAdd, 'restore', setfiltredArray, filtredArray, setremovedResult, removedResult)
                                    }} >

                                        <FontAwesomeIcon icon={faRotateLeft} />
                                        <div>שחזור</div>
                                    </div>
                                    <FontAwesomeIcon icon={faXmark} />
                                </div>
                            </div>
                        </div>

                        <div id={`textareawrap${r.M_id}`} className={styles.expandedcomment}>
                            <div className={styles.commentwrapper_} onClick={(e) => {
                                computeLength2(e, setlettersNumber, lettersNumber, r.M_id)
                                callResultMemo(e, r.M_id, settextareatext, textareatext)
                            }} >
                                <div className={styles.calresult}  >אין תשובה</div>
                                <div className={styles.calresult}  >  טלפון שגוי</div>
                                <div className={styles.calresult}  >  לא רלוונטי</div>
                                <div className={styles.calresult}  >  להתקשר החל מהשעה</div>
                                <div className={styles.calresult}  >  להתקשר מאוחר יותר</div>
                            </div>
                            <textarea id={`textarea${r.M_id}`}
                                onChange={(e) => {
                                    computeLength(e, setlettersNumber, lettersNumber)
                                    settextareatext(e.target.value)
                                }} className={styles.textareawhite} placeholder='כתיבת הערה..'>

                            </textarea>

                            <div className={styles.counterandbuttons}  >
                                <div className={styles.counter} >{`${lettersNumber}/200`}</div>
                                <div className={styles.buttonsrow} id='buttons'>
                                    <div id='cancel' className={styles.cancelbutton} onClick={(e) => condenseTextArea(e, r.M_id)} >ביטול</div>
                                    <div id='save' className={styles.savebutton} onClick={(e) => condenseTextArea(e, r.M_id)} >שמירה</div>

                                </div>

                            </div>

                        </div>


                    </div>


                )}


                <div className={styles.footer} ></div>
            </div>}


            {favAdd.length === 0 &&
                <div className={styles.altfav}>

                    <div className={styles.topwrapper}>
                        <div className={styles.toptitle}  >מודעות שאהבתי</div>
                        <div className={styles.accessability}> <FontAwesomeIcon className={styles.personicon} icon={faPerson} /> <div>נגישות</div></div>
                    </div>

                    <img src={girl} className={styles.altgirl} ></img>
                </div>


            }
        </div>

    )
}

export default FavPage