import React, { useState, useEffect } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faGreaterThan } from '@fortawesome/free-solid-svg-icons'
import { faQuestion } from '@fortawesome/free-solid-svg-icons'
import styles from './AdvancedSearch.module.scss'
import DatePicker from "react-datepicker";
import { useForm } from "react-hook-form";
import { useGlobalContext } from '../../redux/actions'
import { advancedSearch } from '../functions/searchResults.js'
import Results from './Results'

import "react-datepicker/dist/react-datepicker.css";

function AdvancedSearch({ setresults, results, chosenCity, chosenStreet, Search, priceFrom, priceTo,
    roomNumberFrom, roomsNumberTo, setadvancedSearchPanel,
    setchosenCity,
    setchosenStreet,
    setpriceFrom,
    setpriceTo,
    setroomNumberFrom,
    setroomsNumberTo, setestateKind, settransfer, setresultAmount }) {
    const { register, reset, handleSubmit, watch, formState: { errors } } = useForm();

    const [fa1, setfa1] = useState(false)
    const [fa2, setfa2] = useState(false)
    const [startDate, setStartDate] = useState();
    const { loggedName } = useGlobalContext()
    useEffect(() => { setresults([]) }, [])
    function onSubmit(data) {
        let startDateModified = startDate?.toLocaleDateString('en-US', { year: 'numeric', month: '2-digit', day: '2-digit', })
        advancedSearch([data, startDateModified, setresults, chosenCity, chosenStreet, Search, priceFrom, priceTo,
            roomNumberFrom, roomsNumberTo], loggedName)

    }

    return (
        <div>

            <form onSubmit={handleSubmit(onSubmit)}  >
                <div className={styles.bar}></div>
                <div className={styles.title}>מאפייני דירה</div>

                <div className={styles.propswrapper}>




                    <div className={styles.flexprops}>
                        <span className={styles.propstitle}>   <input {...register("parking")} className={styles.checkbox} type='checkbox'></input> <span>חניה</span> </span>
                        <span className={styles.propstitle} >   <input {...register("bars")} className={styles.checkbox} type='checkbox'></input> <span>סורגים</span> </span>
                        <span className={styles.propstitle} >   <input {...register("solitary")} className={styles.checkbox} type='checkbox'></input> <span>בבלעדיות</span> </span>
                    </div>

                    <div className={styles.flexprops} >
                        <span className={styles.propstitle}>   <input {...register("elevator")} className={styles.checkbox} type='checkbox'></input> <span>מעלית</span> </span>
                        <span className={styles.propstitle} >   <input {...register("shelter")} className={styles.checkbox} type='checkbox'></input> <span>מחסן</span> </span>
                    </div>


                    <div className={styles.flexprops} >
                        <span className={styles.propstitle}>   <input {...register("mazgan")} className={styles.checkbox} type='checkbox'></input> <span>מיזוג</span> </span>
                        <span className={styles.propstitle} >   <input {...register("disabled")} className={styles.checkbox} type='checkbox'></input> <span>גישה לנכים</span> </span>
                    </div>

                    <div className={styles.flexprops} >
                        <span className={styles.propstitle}>   <input {...register("balcony")} className={styles.checkbox} type='checkbox'></input> <span>מרפסת</span> </span>
                        <span className={styles.propstitle} >   <input {...register("repaired")} className={styles.checkbox} type='checkbox'></input> <span>משופצת</span> </span>
                    </div>

                    <div className={styles.flexprops} >
                        <span className={styles.propstitle}>   <input {...register("mamad")} className={styles.checkbox} type='checkbox'></input> <span>ממ"ד</span> </span>
                        <span className={styles.propstitle} >   <input {...register("furniture")} className={styles.checkbox} type='checkbox'></input> <span>מרוהטת</span> </span>
                    </div>


                </div>

                <div className={styles.bar}></div>

                <div className={styles.flex} >



                    <div   >

                        <label className={styles.label} >     קומה</label>
                        <div className={styles.flex} >
                            <div className={styles.fontwrapper1}>  <FontAwesomeIcon className={!fa1 ? styles.fa1 : styles.fa1alt} icon={faGreaterThan}></FontAwesomeIcon></div>
                            <div className={styles.fontwrapper1}>  <FontAwesomeIcon className={!fa2 ? styles.fa2 : styles.fa2alt} icon={faGreaterThan}></FontAwesomeIcon></div>

                            <select onClick={() => setfa1(!fa1)} {...register("floorFrom")} className={styles.select1}   >
                                <option value='' disabled selected hidden>        מ-     </option>

                                <option>1</option>
                                <option>2</option>
                                <option>3</option>
                                <option>4</option>
                                <option>5</option>
                                <option>6</option>
                                <option>7</option>
                                <option>8</option>
                                <option>9</option>
                                <option>10</option>


                            </select>

                            <select onClick={() => setfa2(!fa2)} {...register("floorTo")} className={styles.select1}  >
                                <option value='' disabled selected hidden>         עד-   </option>

                                <option>1</option>
                                <option>2</option>
                                <option>3</option>
                                <option>4</option>
                                <option>5</option>
                                <option>6</option>
                                <option>7</option>
                                <option>8</option>
                                <option>9</option>
                                <option>10</option>
                            </select>

                        </div>

                    </div>

                    <div >
                        <label className={styles.label}   >גודל דירה  (במ"ר) </label>
                        <div className={styles.flex} >
                            <input {...register("sizeFrom")} className={styles.select} placeholder='    מ-' ></input>
                            <input {...register("sizeTo")} className={styles.select} placeholder='    עד-' ></input>
                        </div>
                    </div>



                    <div>
                        <label className={styles.label} >     תאריך כניסה      </label>
                        <div  >   <DatePicker

                            dateFormat="MM/dd/yyyy"
                            minDate={new Date()}
                            selected={startDate}
                            className={styles.row}
                            placeholderText=" החל מ- הזינו תאריך   "
                            onChange={(date) => setStartDate(date)} />  </div>
                    </div>

                    <div className={styles.flex2}>
                        <input className={styles.checkbox} type='checkbox'></input>
                        <div className={styles.title2} >כניסה מיידית</div>
                    </div>






                </div>
                <div className={styles.bar2}></div>

                <div className={styles.flex3} >

                    <div className={styles.flexcol}  ><label className={styles.title2}  >חיפוש חופשי</label>
                        <input {...register("description")}  ></input></div>


                    <div className={styles.flex4} >
                        <input className={styles.checkbox} type='checkbox'></input>
                        <div className={styles.title3} >הצגת מושבים וקיבוצים בלבד</div>
                        <FontAwesomeIcon className={styles.question} icon={faQuestion} ></FontAwesomeIcon>

                    </div>

                </div>


                <div className={styles.bar}></div>

                <span className={styles.lastspan} >
                    <button type='submit' className={styles.button} >חיפוש</button>
                    <span className={styles.title6} onClick={() => {
                        reset()
                        setadvancedSearchPanel(false)
                        setresults([])
                        setchosenCity('')
                        setchosenStreet('')
                        setpriceFrom(false)
                        setpriceTo(false)
                        setroomNumberFrom('')
                        setroomsNumberTo('')
                        setestateKind([])
                        setresultAmount(10)
                    }}  >נקה</span>
                </span>
            </form>
            <Results results={results} setresults={setresults}  ></Results>
        </div>
    )
}

export default AdvancedSearch