import React, { useState, useEffect } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { useGlobalContext } from '../../redux/actions'

import styles from './NavbarMenu.module.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCar, faSearch, faUser } from '@fortawesome/free-solid-svg-icons'
import { faHeart } from '@fortawesome/free-solid-svg-icons'
import { faBars } from '@fortawesome/free-solid-svg-icons'
import { faXmark } from '@fortawesome/free-solid-svg-icons'
import { faHouse } from '@fortawesome/free-solid-svg-icons'
import { faCouch } from '@fortawesome/free-solid-svg-icons'
import { faBusinessTime } from '@fortawesome/free-solid-svg-icons'
import { faPaw } from '@fortawesome/free-solid-svg-icons'
import sidebar2 from '../pics/sidebar2.png'

import { faGraduationCap } from '@fortawesome/free-solid-svg-icons'

function NavbarMenu({ setMenu, isLogged, eliminateNavbar, menu, loggedName }) {
    return (
        <div className={styles.modal}>

            <div className={styles.innermodal}>

                <FontAwesomeIcon onClick={() => setMenu(!menu)} className={styles.close} icon={faXmark} ></FontAwesomeIcon>

                {isLogged && <div className={styles.mainwrap}>
                    <div className={styles.firstletter}>{loggedName.slice(0, 1)}</div>
                    <div className={styles.center}>
                        <div className={styles.nametitle} >{loggedName}</div>
                        <div className={styles.privatetitle}  >לאיזור האישי</div>
                    </div>
                    <div className={styles.exit}  >התנתקות</div>
                </div>}
                {!isLogged &&
                    <div className={styles.center}>
                        <FontAwesomeIcon className={styles.person2} icon={faUser}></FontAwesomeIcon>
                        <Link onClick={() => {
                            setMenu(!menu)
                            eliminateNavbar()
                        }} className={styles.connect} to='./login-register' state={'regular-login'}>התחברות</Link>
                    </div>
                }

                <Link onClick={() => {
                    setMenu(!menu)
                    eliminateNavbar()
                }}
                    className={styles.greenbtn} to={!isLogged ? './login-register' : './publish'} state={'publish-login'}>     פרסום מודעה        </Link>


                <div className={styles.sidebarmenu}>

                    <div className={styles.sidebarmenutitle}>חיפוש מהיר באתר</div>

                    <div onClick={() => setMenu(!menu)} className={styles.menuwrapper}  >  <FontAwesomeIcon className={styles.iconsinterval} icon={faHouse} ></FontAwesomeIcon>
                        <Link to='./searchRE' className={styles.menutext} >נדל"ן</Link></div>

                    <div className={styles.menuwrapper} >  <FontAwesomeIcon className={styles.iconsinterval} icon={faCar} ></FontAwesomeIcon><Link className={styles.menutext} >  רכב </Link></div>
                    <div className={styles.menuwrapper} >  <FontAwesomeIcon className={styles.iconsinterval} icon={faCouch} ></FontAwesomeIcon><Link className={styles.menutext} >  יד שניה </Link></div>
                    <div className={styles.menuwrapper} >  <FontAwesomeIcon className={styles.iconsinterval} icon={faBusinessTime} ></FontAwesomeIcon><Link className={styles.menutext} >  עסקים   </Link></div>
                    <div className={styles.menuwrapper} >  <FontAwesomeIcon className={styles.iconsinterval} icon={faSearch} ></FontAwesomeIcon><Link className={styles.menutext} >    דרושים </Link></div>
                    <div className={styles.menuwrapper} >  <FontAwesomeIcon className={styles.iconsinterval} icon={faPaw} ></FontAwesomeIcon><Link className={styles.menutext} >    חיות מחמד </Link></div>
                    <div className={styles.menuwrapper} >  <FontAwesomeIcon className={styles.iconsinterval} icon={faGraduationCap} ></FontAwesomeIcon><Link className={styles.menutext} >      לימודים </Link></div>


                </div>
            </div>
            <img className={styles.sidebar2} src={sidebar2}></img>
            <Link to='./lastSearches' className={styles.Lsearches}  ></Link>
            <Link to='./favPage' className={styles.Lfavadds} ></Link>



        </div>
    )
}

export default NavbarMenu