import React from 'react'
import styles from './PersonalInfoLogged.module.scss'
import { Link, useNavigate } from 'react-router-dom'
import user from '../../pics/person.png'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars, faDoorOpen } from '@fortawesome/free-solid-svg-icons'
import { faArrowRightArrowLeft } from '@fortawesome/free-solid-svg-icons'
import { faClockRotateLeft } from '@fortawesome/free-solid-svg-icons'
function PersonalInfoLogged({ setactivateprivateAreaInfo, setactivateprivateAreaInfominus, disconnect }) {
    const navigate = useNavigate()
    return (
        <div className={styles.personalInfo}
            onMouseLeave={() => {
                setactivateprivateAreaInfo(false)
                setactivateprivateAreaInfominus(false)
            }}
        >

            <div className={styles.privatearea} onClick={() => setactivateprivateAreaInfo(false)} >
                <Link className={styles.Linkflex} to='./personalzone'>   <img className={styles.smallperson} src={user} /> <div >   איזור אישי</div></Link>
            </div>

            <div className={styles.privatearea} >
                <FontAwesomeIcon className={styles.iconmargin} icon={faArrowRightArrowLeft} /> <div > השוואת רכבים</div>
            </div>

            <Link className={styles.Linkdecoration} to='./lastSearches' > <div className={styles.privatearea}  >
                <FontAwesomeIcon className={styles.iconmargin} icon={faClockRotateLeft} /> <div >   חיפושים אחרונים</div>
            </div>
            </Link>
            <div className={styles.privatearea} onClick={(e) => {
                disconnect(e)
                setactivateprivateAreaInfo(false)
                setactivateprivateAreaInfominus(false)

                navigate('..')
            }}>
                <FontAwesomeIcon className={styles.iconmargin} icon={faDoorOpen} /> <div >   התנתקות</div>
            </div>

        </div>
    )
}

export default PersonalInfoLogged