import React, { useState, useEffect } from 'react'
import styles from './FavModal.module.scss'
import { useGlobalContext } from '../../redux/actions'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faShekelSign } from '@fortawesome/free-solid-svg-icons'
import { faHeart } from '@fortawesome/free-solid-svg-icons'
import { faChevronLeft } from '@fortawesome/free-solid-svg-icons'
import { faChevronRight } from '@fortawesome/free-solid-svg-icons'
import { faPhone } from '@fortawesome/free-solid-svg-icons'
import { faXmark } from '@fortawesome/free-solid-svg-icons'
import { faCheck } from '@fortawesome/free-solid-svg-icons'
import { faBell } from '@fortawesome/free-solid-svg-icons'
import { slideImage, showPhoneNum, validateInput } from './navbarUtils.js'
import { useForm } from "react-hook-form";
import sidebar3 from '../pics/sidebar3.png'
import GreyFav from './GreyFav'

import { Link, useNavigate } from "react-router-dom";


function FavModal({ sethoveringoverheart }) {

    const { favAdd } = useGlobalContext()
    const [modaladdInfo, setmodaladdInfo] = useState(false)
    const [currentImg, setcurrentImg] = useState('')
    const [imgcounter, setimgcounter] = useState(0) // picture slide-show counter
    const [totalimgcounter, settotalimgcounter] = useState(0) // picture slide-show counter
    const [showPhone, setshowPhone] = useState(false)
    const [mailmodal, setmailmodal] = useState(false)
    const [advancedMailModal, setadvancedMailModal] = useState(false)
    const [activatebutton, setactivatebutton] = useState(false)

    const { register, reset, handleSubmit, watch, formState: { errors } } = useForm();
    const values = watch()
    const navigate = useNavigate();


    return (
        <div id='mainModalFavourite' className={styles.main}>


            {favAdd.map((f, index) =>
                <div id={index}
                    onMouseEnter={() => document.getElementById(index).style.boxShadow = '2px 2px 4px 2px rgb(12 0 51 / 10%)'}
                    onMouseLeave={() => document.getElementById(index).style.boxShadow = 'none'}
                    onClick={() => {
                        setcurrentImg(f.main)
                        setmodaladdInfo(true)
                    }}
                    className={styles.mainflex}>

                    <div   >
                        <FontAwesomeIcon className={styles.orangeheart} icon={faHeart} />
                        <img className={styles.imgsize} src={f.main} ></img>
                    </div>

                    <div className={styles.flexcol}  >
                        <div>{`${f.street} ${f.houseNumber}`}</div>
                        <div className={styles.minitext}>{`${f.kind}, ${f.settlement}`}</div>
                    </div>


                    <div className={styles.niswrapper} >
                        {(f.price > 0) ? f.price : 'אין מחיר'}
                        {(f.price > 0) && <FontAwesomeIcon className={styles.nissign} icon={faShekelSign} ></FontAwesomeIcon>}
                    </div>











                    {modaladdInfo &&
                        <GreyFav f={f} setcurrentImg={setcurrentImg} setimgcounter={setimgcounter} settotalimgcounter={settotalimgcounter}
                            setmodaladdInfo={setmodaladdInfo} currentImg={currentImg} totalimgcounter={totalimgcounter} imgcounter={imgcounter}
                            setmailmodal={setmailmodal} index={index}
                        ></GreyFav>
                    }



                    {mailmodal &&
                        <div className={styles.modal}>


                            <div className={styles.innermodal}>
                                <div className={styles.title3} >כדאי לשים לב</div>
                                <div className={styles.sendtext}  >אנו ממליצים להמנע מהעברת כספים טרם קבלת המוצר ולהיזהר מעסקאות בהן המוצר צפוי להישלח אליכם לאחר תשלום</div>
                                <div className={styles.send}>
                                    <div onClick={() => setmailmodal(false)} >חזרה</div>
                                    <div onClick={(e) => {

                                        setmailmodal(false)
                                        setadvancedMailModal(true)
                                    }}>שליחת הודעה</div>
                                </div>
                            </div>
                        </div>}

                    {advancedMailModal &&
                        <div className={styles.modal}>
                            <div className={styles.innermodal2}>

                                <div className={styles.mailflex}>
                                    <div>שליחת דוא"ל למפרסם</div>
                                    <FontAwesomeIcon onClick={() => setadvancedMailModal(false)} className={styles.cursor} icon={faXmark}    ></FontAwesomeIcon>
                                </div>

                                <form onChange={() => validateInput(values, setactivatebutton)} >
                                    <div>
                                        <input {...register("clientName")} className={styles.mailinput} placeholder='השם שלך'></input>
                                        <input {...register("clientPhone")} className={styles.mailinput} placeholder='מספר הטלפון שלך' ></input>
                                        <input {...register("clientMail")} className={styles.mailinput} placeholder='כתובת דוא"ל לקבלת תושבה'></input>
                                    </div>
                                </form>

                                <textarea {...register("clientDescription")} className={styles.textarea} placeholder='תיאור' ></textarea>


                                {!activatebutton && <div className={styles.buttonswrapper}>
                                    <button className={styles.cancel0} >ביטול</button>
                                    <button className={styles.sendmailorangebutton0} >שליחה</button>

                                </div>}
                                {activatebutton && <div className={styles.buttonswrapper}>
                                    <div className={styles.cancel} onClick={() => {
                                        setadvancedMailModal(false)
                                    }}  >ביטול</div>
                                    <button onClick={() => setadvancedMailModal(false)} className={styles.sendmailorangebutton} >שליחה</button>

                                </div>}
                            </div>
                        </div>
                    }








                </div>)}
            <Link className={styles.tofavourites} to='./favPage' >
                <div className={styles.orangetofavgrey}   >לכל המודעות שסימנתי</div>
            </Link>
        </div>
    )
}

export default FavModal