import React, { useContext, useReducer } from 'react'
import reducer from './reducer'



const AppContext = React.createContext()

const initialState = {
    isLogged: false,
    loggedName: 'אורח/ת',
    loggedfamilyname: '',
    loggedphone: '',
    loggedmail: '',
    REformFlow: 1,
    REformFlowAbsolute: 1,



    Object: {
        settlement: '',
        street: '',
        kind: '',

        condition: '',
        floorNumber: null,
        totalFloorNumber: null,
        numberOfRooms: null,
        numberOfParkings: null,
        numberOfBalconies: null,
        quadrateMeter: null,
        quadrateMeterGarden: null,
        quadrateMeterTotal: null,
        contactName: '',
        mainPhoneNumbernull: null,
        paymentPath: null,
        name: '',
        familyName: '',
        phone: null,
        houseNumber: null
    },
    NonMandatoryObject: {
        price: null,
        enteringDate: null,
        bars: false,
        boiler: false,
        citchen: false,
        disabled: false,
        elevator: false,
        furniture: false,
        mamad: false,
        mazgan: false,
        repaired: false,
        shelter: false,
        tadiran: false,
        additionalContactName: '',
        additionalPhone: '',
        email: '',
        description: ''
    },
    Pictures: {
        main: null,
        one: null,
        two: null,
        three: null,
        four: null,
        five: null,
        six: null,
        seven: null,
        eight: null,
        nine: null


    },

    Search: {
        REprops: [],
        roomsFrom: null,
        roomsTo: null

    },
    AdvancedSearch: {
        AC: 0,
        balcony: 0,
        bars: 0,
        disabled: 0,
        elevator: 0,
        furniture: 0,
        parking: 0,
        remonted: 0,
        shelter: 0,
        solitary: 0,
        warehouse: 0,

        description: '',

        enteringDate: null,

        floorFrom: null,
        floorTo: null,
        sizeFrom: null,
        sizeTo: null

    },

    favAdd: [],

    commitedSearches: []





}





const AppProvider = ({ children }) => {
    const [state, dispatch] = useReducer(reducer, initialState)




    function REformMoveStepForward(data) { dispatch({ type: 'FORM_STEP_FORWARD', payload: data }) }
    function REformMoveStepBack(data) { dispatch({ type: 'FORM_STEP_BACK', payload: data }) }
    function REformMoveSpecific(data) { dispatch({ type: 'FORM_STEP_SPECIFIC', payload: data }) }
    function UpdateAbsoluteStep(data) { dispatch({ type: 'UPDATE_ABSOLUTE_STEP', payload: data }) }

    //section2
    function settlementData(data) { dispatch({ type: 'SAVE_SETTLEMENT', payload: data }) }
    function streetData(data) { dispatch({ type: 'SAVE_STREET', payload: data }) }
    function formData(data) { dispatch({ type: 'SAVE_FORM_2_DATA', payload: data }) }

    //section3
    function roomsData(data) { dispatch({ type: 'SAVE_ROOMS', payload: data }) }
    function parkingsData(data) { dispatch({ type: 'SAVE_PARKINGS', payload: data }) }
    function balconiesData(data) { dispatch({ type: 'SAVE_BALCONIES', payload: data }) }
    function NonMandatoryDataStep3(data) { dispatch({ type: 'SAVE_FORM_3_DATA', payload: data }) }

    //section4
    function dataFormStep4(data) { dispatch({ type: 'SAVE_FORM_4_DATA', payload: data }) }

    //section5
    function dataFormStep5(data) { dispatch({ type: 'SAVE_FORM_5_DATA', payload: data }) }

    //section6
    function dataFormStep6(data) { dispatch({ type: 'SAVE_FORM_6_DATA', payload: data }) }

    //section7
    function dataFromStep7(data) { dispatch({ type: 'SAVE_FORM_7_DATA', payload: data }) }



    //general
    function registration(data) { dispatch({ type: 'REGISTRATION', payload: data }) }
    function login(data) { dispatch({ type: 'LOGIN', payload: data }) }
    function disconnect() { dispatch({ type: 'DISCONNECT' }) }


    function favouriteData(data) { dispatch({ type: 'SAVE_FAVOURITE_DATA', payload: data }) }
    function updatefavAdd(data) { dispatch({ type: 'UPDATE_FAV_ADD', payload: data }) }
    function restorefavAdd(data) { dispatch({ type: 'RESTORE_FAV_ADD', payload: data }) }




    //search

    function REprops(data) { dispatch({ type: 'RE_PROPS', payload: data }) }
    function Nrooms(data) { dispatch({ type: 'N_ROOMS', payload: data }) }
    function saveProps(data) { dispatch({ type: 'SAVE_SEARCH_PROPS', payload: data }) }

    function saveCommitedSearches(data) { dispatch({ type: 'SAVE_SEARCH_INFO', payload: data }) } // save to db
    function saveLastSearchInfo(data) { dispatch({ type: 'SAVE_SEARCH_INFO_TO_STATE', payload: data }) } // save from db to state


    //personal zone
    function favouriteAdds(data) { dispatch({ type: 'SAVE_ADD', payload: data }) }





    return (
        <AppContext.Provider value={{
            ...state, REformMoveStepForward, settlementData, streetData, formData, REformMoveStepBack, REformMoveSpecific, UpdateAbsoluteStep, roomsData,
            parkingsData, balconiesData, dataFormStep4, NonMandatoryDataStep3, dataFormStep5, dataFormStep6, dataFromStep7, registration, login,
            REprops, Nrooms, saveProps, favouriteAdds, favouriteData, updatefavAdd, restorefavAdd, disconnect, saveCommitedSearches, saveLastSearchInfo
        }}>{children}</AppContext.Provider>
    )
}
// make sure use
export const useGlobalContext = () => {
    return useContext(AppContext)
}

export { AppContext, AppProvider }

