let textMemo = ''
let textMemo2 = ''
let counter = 0
export function slideImage(e, data, setcurrentImg, setimgcounter, settotalimgcounter) {
    let numberOfImagesinObject = 0
    const images = [data.main,
    data.one,
    data.two,
    data.three]

    for (let i = 0; i < images.length; i++) {
        if (images[i] != '') numberOfImagesinObject += 1

    }
    settotalimgcounter(numberOfImagesinObject)


    if (e.target.id = 'right') {
        counter += 1
        if (counter == numberOfImagesinObject) counter = 0
        if (counter === 0 && (images[counter] != undefined && images[counter] != '')) setcurrentImg(images[counter])
        else if (counter === 1 && (images[counter] != undefined && images[counter] != '')) setcurrentImg(images[counter])
        else if (counter === 2 && (images[counter] != undefined && images[counter] != '')) setcurrentImg(images[counter])
        else if (counter === 3 && (images[counter] != undefined && images[counter] != '')) setcurrentImg(images[counter])

    }

    else if (e.target.id = 'left') {
        counter -= 1
        if (counter < 0) counter = numberOfImagesinObject

        if (counter === 0 && (images[counter] != undefined && images[counter] != '')) setcurrentImg(images[counter])
        else if (counter === 1 && (images[counter] != undefined && images[counter] != '')) setcurrentImg(images[counter])
        else if (counter === 2 && (images[counter] != undefined && images[counter] != '')) setcurrentImg(images[counter])
        else if (counter === 3 && (images[counter] != undefined && images[counter] != '')) setcurrentImg(images[counter])

    }
    setimgcounter(counter)

}

export function showPhoneNum(index, e) {
    e.stopPropagation()
    document.getElementById(index + 1000).style.display = 'none'
    document.getElementById(index + 1050).style.display = 'flex'
    document.getElementById(index + 1050).style.textAlign = 'center'

    e.stopPropagation()
}

export function validateInput(values, setactivatebutton) {
    if (values.clientName.length != undefined && values.clientPhone.length != undefined &&
        values.clientMail.length != undefined && values.clientDescription.length != undefined) {
        setactivatebutton(true)
    }

}

export function expandTextArea(e, id) {
    document.getElementById(`comment${id}`).style.display = 'none'
    document.getElementById(`textareawrap${id}`).style.display = 'flex'
}

export function condenseTextArea(e, id) {
    let textupdate = ''
    if (e.target.id === 'save') {
        if (textMemo.includes('הוספת הערה') && (textMemo2 == '')) {
            textupdate = textMemo.split('הוספת הערה')
            document.getElementById(`newtext${id}`).innerText = textupdate[textupdate.length - 1]

        }
        else if (textMemo2 != '') {
            textupdate = textupdate + ' ' + textMemo2
            document.getElementById(`newtext${id}`).innerText = textupdate
        }
        document.getElementById(`eliminate${id}`).style.display = 'none'
    }

    document.getElementById(`comment${id}`).style.display = 'flex'
    document.getElementById(`textareawrap${id}`).style.display = 'none'

    textMemo = ''
    textMemo2 = ''
}


export function callResultMemo(e, id) {
    document.getElementById(`textarea${id}`).value = e.target.innerText
}

export function computeLength(e, setlettersNumber) {
    setlettersNumber(e.target.value.length)
    textMemo2 = e.target.value
}

export function computeLength2(e, setlettersNumber, lettersNumber, id) {
    textMemo = document.getElementById(`comment${id}`).innerText + e.target.innerText
    setlettersNumber(lettersNumber + e.target.innerText.length)
}

export function changeTextToOrange(id) {
    document.getElementById(`uppertext${id}`).classList.remove('white')
    document.getElementById(`uppertext${id}`).classList.add('newtab')



    document.getElementById(`lowertext${id}`).innerText = 'לחצו לפרטים'
    document.getElementById(`lowertext${id}`).classList.add('neworange')

}

export function cancelChanges(id) {
    document.getElementById(`uppertext${id}`).classList.add('white')
    document.getElementById(`uppertext${id}`).classList.remove('newtab')

    document.getElementById(`lowertext${id}`).innerText = 'עודכן היום'
    document.getElementById(`lowertext${id}`).classList.remove('neworange')

}


let remover
export function updateAdd(id, favAdd, updatefavAdd, type) {

    if (type === 'delete') {
        document.getElementById(id).style.display = 'none'
        document.getElementById(`textareafirst${id}`).style.display = 'none'

        document.getElementById(`deleted${id}`).style.display = 'flex'

        remover = setTimeout(() => {
            document.getElementById(`deleted${id}`).style.display = 'none'
            updatefavAdd(favAdd.filter(f => f.M_id != id))
        }, 4000)


    }
    else if (type === 'restore') {
        clearTimeout(remover)
        document.getElementById(id).style.display = 'flex'
        document.getElementById(`textareafirst${id}`).style.display = 'flex'
        document.getElementById(`deleted${id}`).style.display = 'none'
    }


}

