import React, { useState, useEffect } from 'react'
import { useGlobalContext } from './redux/actions'
import ImageOne from './components/Home/ImageOne'
import Img2 from './components/Home/Img2'
import HomePageSemiFooter from './components/Home/HomePageSemiFooter'

function Home() {
    const [swappic, setswappic] = useState(true)


    useEffect(() => {
        const myInterval = setInterval(() => setswappic(!swappic), 5000)
        return () => clearInterval(myInterval)
    },
        [swappic])

    useEffect(() => {
        alert('hi')
    },
        [])


    return (
        <>

            {swappic &&
                <ImageOne />}


            {!swappic &&
                <Img2 />
            }

            <HomePageSemiFooter />



        </>

    )
}

export default Home